import JSTypeInfo from '../engine-info/JSType-Info.js';
import sizeInfo from '../engine-info/ObjectSize.js';
import Utils from '../basic/Utils.js';
import RtConfig from '../basic/Config.js';
import sharedObj from '../basic/Global.js';
/**
 * A unified data interface that provides a centralized
 * data management and data manipulation interface
 */
class DataPreprocessor {
    compressCallback(label, value) {
        if (label !== 'buffInfo' || !value || !value.length) return value;
        return new Uint16Array(value);
    }
    compress(snapshot) {
        sharedObj.emit('show-status', 'compressing snapshot...');
        this.traverse(snapshot, this.compressCallback);
        return snapshot;
    }
    /**
     * traverse all (nested) properties of an object,
     * assuming there is no cyclic reference
     * @param {*} parent 
     * @param {*} callback 
     */
    traverse(parent, callback) {
        if (!parent || Utils.isPrimitive(parent)) return;
        for (let prop in parent) {
            if (!Utils.HOP(parent, prop)) continue;
            parent[prop] = callback(prop, parent[prop]);
            this.traverse(parent[prop], callback);
        }
    }
}

export default new DataPreprocessor();


