import Utils from '../../basic/Utils.js';
import dataInterface from '../../data/DataInterface.js';

/**
 * detect objects that are used as a dictionary,
 * they are better off switched to map, which is more memory efficient
 */
class DirectionaryChecker {
    constructor() {
        this.dictionaryPropNumThreshold = 64;
    }
    isDictionary(object) {
        if (!object || !object.properties) return false;
        let propCnt = object.properties.length | 0;
        return propCnt > this.dictionaryPropNumThreshold;
    }
    check(object, treeNode) {
        // guard checking
        if (!Utils.HOP(object, 'objectId')) return;
        if (!Utils.isJsType(treeNode, 'Object')) return;
        // set the flag in the UI tree node
        if (this.isDictionary(object)) {
            Utils.addIssue(treeNode, {
                type: 'dictionary-object',
                name: `Dynamic object as a dictionary
                       (Map is more memory-efficient)`,
            });
        }
    }
}

export default new DirectionaryChecker();



