/**
 * Utils functions
 * 
 * Author: Liang Gong
 */
import {js_beautify} from '../external/beautify.js';
import RtConfig from '../basic/Config.js';
import ViewUtils from './ViewUtils.js';

let _hop = Object.prototype.hasOwnProperty;
let _Object = Object;
let _JSON = JSON;
let _toFixed = (0).toFixed;
let _parseInt = parseInt;
let _log = RtConfig.debugMode ? console.log.bind(console) : () => {};
let Utils = {
    HOP: function(obj, prop) {
        if (!obj) return false;
        return _hop.call(obj, prop);
    },
    log: _log,
    parseInt(str, base) {
        base = base ? (base | 0) : 10;
        return _parseInt(str, base);
    },
    constants: {
        GB: 1024 * 1024 * 1024,
        MB: 1024 * 1024,
        KB: 1024,
    },
    toFixed(num, digits) {
        return _toFixed.call(num, [digits]);
    },
    readableSize(sizeNum) {
        let GB = Utils.constants.GB,
            MB = Utils.constants.MB,
            KB = Utils.constants.KB;
        if (((sizeNum / GB) | 0) > 0) {
            return Utils.toFixed((sizeNum / GB), 2) + 'GB';
        } else if (((sizeNum / MB) | 0) > 0) {
            return Utils.toFixed((sizeNum / MB), 2) + 'MB';
        } else if (((sizeNum / KB) | 0) > 0) {
            return Utils.toFixed((sizeNum / KB), 2) + 'KB';
        }
        return sizeNum;
    },
    stringifyReplacer(key, value) {
        // add ... at the end of the buffInfo that is omitted
        if (key === 'addInfo' && value && value.buffInfo) {
            if (value.length > value.buffInfo.length) {
                let ret = _JSON.parse(_JSON.stringify(value));
                ret.buffInfo = Array.from(value.buffInfo);
                ret.buffInfo.push(`..., len: ${value.length}`);
                return ret;
            }
        }

        if (key === 'addInfo' && value && 
            value.arrayInfo && Utils.isArr(value.arrayInfo)) {
            if (value.arrayInfo.length > 3) {
                let ret = _JSON.parse(_JSON.stringify(value));
                for (let i = 0; i < value.arrayInfo.length; i++) {
                    let originIndices = value.arrayInfo[i].arrIndices;
                    ret.arrayInfo[i].arrIndices =
                        Array.from(originIndices).slice(0, 64)
                            .concat(`..., len: ${originIndices.length}`);
                    // console.log(ret.arrayInfo[i].arrIndices);
                }
                return ret;
            }
        }

        if (Utils.isArr(value) && value.length <= 512) {
            return value;
        } else if (Utils.isArr(value)) {
            return Array.from(value).slice(0, 512)
                .concat(`..., len: ${value.length}`);
            /*let ret = '[';
            for (let i = 0; i < 1000; i++) {
                ret += value[i] + ', ';
            }
            ret += ` ... (length: ${value.length})]`;
            return ret;
            */
        }
        return value;
    },
    stringify(obj) {
        let ret;
        try {
            ret = _JSON.stringify(obj, Utils.stringifyReplacer, 0);
            ret = js_beautify(ret, { indent_size: 2 });
        } catch (ex) {
            ret = ex + '\n' + 'could be caused by out of memory due to large object size';
        } finally {
            return ret;
        }
    },
    isPrimitive(val) {
        let type = typeof val;
        return (
            type === 'string' ||
            type === 'symbol' ||
            type === 'number' ||
            type === 'boolean' ||
            type === 'undefined'
        );
    },
    isArr: function(obj) {
        return (
            Array.isArray(obj) ||
            (obj &&
                obj.constructor &&
                (obj instanceof Uint8Array ||
                    obj instanceof Uint16Array ||
                    obj instanceof Uint32Array ||
                    obj instanceof Uint8ClampedArray ||
                    obj instanceof ArrayBuffer ||
                    obj instanceof Int8Array ||
                    obj instanceof Int16Array ||
                    obj instanceof Int32Array ||
                    obj instanceof Float32Array ||
                    obj instanceof Float64Array))
        );
    },
    getOwnPropNames: function(obj) {
        let ret = [], names = _Object.getOwnPropertyNames(obj);
        for (let name of names) {
            if (!Utils.HOP(obj, name)) continue;
            ret.push(name);
        }
        return ret;
    },
};

// copy all functions from ViewUtils to Utils
for (let prop in ViewUtils) {
    if (!Utils.HOP(ViewUtils, prop)) continue;
    if (Utils.HOP(Utils, prop)) 
        Utils.log(`[!]: ${prop} in ViewUtils overrides Utils`);
    Utils[prop] = ViewUtils[prop];
}

export default Utils;



