import JSTypeInfo from '../engine-info/JSType-Info.js';
import sizeInfo from '../engine-info/ObjectSize.js';
import Utils from '../basic/Utils.js';
import RtConfig from '../basic/Config.js';
import sharedObj from '../basic/Global.js';
import dataPreprocessor from '../data/DataPreprocessor.js';
/**
 * A unified data interface that provides a centralized
 * data management and data manipulation interface
 */
class DataInterface {
    constructor() {
        this.propJSONLoaded = false;
        this.snapshotJSONLoaded = false;
        this.allocTraceJSONLoaded = false;
        this.allocSiteSorted = false;
        this.propJSON = {
            global: {},
        };
        this.snapshotJSON = {
            properties: [],
        };
        this.propIndex = [];
        this.objectIndex = [];
        this.typeIndex = [];
    }
    /**
     * setter for the allocTrace raw data
     * @param {*} allocTraceJSON
     */
    setAllocTraceJSON(allocTraceJSON) {
        if (this.allocTraceJSONLoaded) return;
        this.allocTraceJSON = allocTraceJSON.allocations;
        this.getFileMap(allocTraceJSON.fileToSourceMap);
        this.allocTraceJSONLoaded = true;
        this.sortAllocationSites();
    }
    getFileMap(map) {
        if (!map || !Utils.isArr(map)) return;
        this.fileNameToSourceMap = {};
        this.fileIdToFileNameMap = [];
        for (let item of map) {
            if (!item || !item.filename || !item.source) continue;
            this.fileNameToSourceMap[item.filename] = item.source;
            this.fileIdToFileNameMap[item.fileId] = item.filename;
        }
    }
    /**
     * setter for the propJSON raw data
     * @param {*} propJSON 
     */
    setPropJSON(propJSON) {
        if (this.propJSONLoaded) return;
        this.propJSON = propJSON;
        // add internal propery names
        this.internalPropNameToPropId = {};
        this.addInternalPropName(propJSON, '<buffer>');
        this.addInternalPropName(propJSON, '<object-array>');
        // construct index for property Id
        this.propIndex = [];
        this.propNameToPropId = new Map();
        for (let prop of propJSON.properties) {
            if (!prop || !prop.propertyId) continue;
            this.propIndex[prop.propertyId] = prop;
            this.propNameToPropId.set(prop.name, prop.propertyId);
        }
        this.propJSONLoaded = true;
        // build index for source code
        // this.getSourceCode();
        this.sortAllocationSites();
    }
    /**
     * setter for the snapshotJSON raw data
     * @param {*} snapshotJSON 
     */
    setSnapshotJSON(snapshotJSON) {
        if (this.snapshotJSONLoaded) return;
        // compress the snapshot to reduce memory footprint
        snapshotJSON = dataPreprocessor.compress(snapshotJSON);
        this.snapshotJSON = snapshotJSON;
        // construct index for object Id
        this.objectIndex = [];
        for (let obj of snapshotJSON.objects) {
            if (!obj || !obj.objectId) continue;
            this.objectIndex[obj.objectId] = obj;
        }
        for (let obj of snapshotJSON.primitives) {
            if (!obj || !obj.primitiveId) continue;
            this.objectIndex[obj.primitiveId] = obj;
        }
        // construct index for type Id
        this.typeIndex = [];
        for (let type of snapshotJSON.objectTypes) {
            if (!type || !type.typeId) continue;
            this.typeIndex[type.typeId] = type;
        }
        // construct rootList checker
        this.rootListSet = [];
        for (let item of snapshotJSON.rootList) {
            this.rootListSet[item.objectId] = true;
        }
        // construct points to relation of all objects
        this.pointsToBy = this.getPointsToMap(snapshotJSON);
        // construct the scopeArrayIndex
        this.scopeArrayIndex = this.getScopeArrayIndex(snapshotJSON);
        // construct the slotArrayIndex
        this.slotArrayIndex = this.getSlotArrayIndex(snapshotJSON);
        // construct the functionBodyIndex
        this.functionBodyIndex = this.getFunctionBodyIndex(snapshotJSON);
        // construct the enclosing scope map
        this.enclosingScopeMap = this.getEnclosingScopeMap(snapshotJSON);
        // construct the kept alive map
        this.keptAliveMap = this.getKeptAliveMap(snapshotJSON);
        // build index for source code
        // this.getSourceCode();
        // build index from slotId to ScopeId
        this.slotToScopeIndex = this.getSlotToScopeIndex(snapshotJSON);
        // sort root-list, objects, primitives based on their sizes
        this.sortBasedOnSizes(snapshotJSON);
        this.snapshotJSONLoaded = true;
        // sort the allocation sites
        this.sortAllocationSites();
    }
    getSourceCode() {
        if (this.sourceIdx) return;
        if (!this.snapshotJSON || !this.propJSON) return;
        if (!this.snapshotJSON.rootList) return;
        this.sourceIdx = {};
        sharedObj.emit('show-status', 'indexing source...');
        for (let item of this.snapshotJSON.rootList) {
            if (!item || !item.objectId) continue;
            let object = this.queryObject(item.objectId);
            if (!object) continue;
            let script = this.getItemByPropName(object, 'script');
            if (!script) continue;
            let filename = this.getItemByPropName(script, 'filename');
            let source = this.getItemByPropName(script, 'source');
            if (!filename.stringVal || !source.stringVal) continue;
            this.sourceIdx[filename.stringVal] = source.stringVal;
        }
    }
    /**
     * return the meta data (in the snapshot) pointed to by a property Id
     * @param {object} parent the parent meta data (in the snapshot) containing the property
     * @param {string} propName the name string of the property
     */
    getItemByPropName(parent, propName) {
        if (!parent || !parent.properties ||
            !Utils.isArr(parent.properties)) return null;
        let propId = this.queryPropId(propName);
        for (let prop of parent.properties) {
            if (!prop || !prop.propertyId) continue;
            if (prop.propertyId !== propId) continue;
            if (!prop.ptrIdVal) continue;
            return this.queryObject(prop.ptrIdVal);
        }
        return null;
    }
    addInternalPropName(propJSON, name) {
        if (!propJSON.properties) return;
        let idx = propJSON.properties.length;
        propJSON.properties.push({
            propertyId: idx,
            name: name
        });
        this.internalPropNameToPropId[name] = idx;
    }
    sortAllocationSites() {
        if (!this.propJSONLoaded || 
            !this.snapshotJSONLoaded || 
            !this.allocTraceJSONLoaded) {
            return;
        }
        if (this.allocSiteSorted) return;
        if (this.allocTraceJSON) {
            sharedObj.emit('show-status', 'sorting allocation sites...');
            // sort the allocation sites
            this.allocTraceJSON.sort((v1, v2) => {
                return sizeInfo.sizeOfAllocationSite(v2) - 
                    sizeInfo.sizeOfAllocationSite(v1);
            });
            // sort the objects in each allocation sites
            for (let site of this.allocTraceJSON) {
                if (!site || !site.objectIds) continue;
                site.objectIds.sort((id1, id2) => {
                    return sizeInfo.sizeof(id2) - sizeInfo.sizeof(id1);
                });
            }
            this.allocSiteSorted = true;
            sharedObj.emit('load-allocTrace', {allocations: this.allocTraceJSON});
            sharedObj.emit('clear-status');
        }
    }
    sortBasedOnSizes(snapshotJSON) {
        sharedObj.emit('show-status', 'sorting root list...');
        // sort the rootlist
        if (snapshotJSON.rootList) {
            snapshotJSON.rootList.sort((v1, v2) => {
                return sizeInfo.sizeof(v2.objectId) - sizeInfo.sizeof(v1.objectId);
            });
        }
        sharedObj.emit('show-status', 'sorting objects...');
        // sort the objects
        if (snapshotJSON.objects) {
            snapshotJSON.objects.sort((v1, v2) => {
                return sizeInfo.sizeof(v2.objectId) - sizeInfo.sizeof(v1.objectId);
            });
        }
        sharedObj.emit('show-status', 'sorting primitives');
        // sort the primitives
        if (snapshotJSON.primitives) {
            snapshotJSON.primitives.sort((v1, v2) => {
                return sizeInfo.sizeof(v2.primitiveId) - sizeInfo.sizeof(v1.primitiveId);
            });
        }
        sharedObj.emit('show-status', 'sorting closures');
        // sort the closures
        if (snapshotJSON.slotArrays) {
            for (let slotItem of snapshotJSON.slotArrays) {
                if (!slotItem || !slotItem.slotId) continue;
                slotItem.__KEEPER_CNT__ = this.getKeeperCountBySlotId(slotItem.slotId);
            }
            snapshotJSON.slotArrays.sort((v1, v2) => {
                if (!v1 || !v1.slotInfo || !v2 || !v2.slotInfo) return -1;
                let k1 = v1.__KEEPER_CNT__;
                let k2 = v2.__KEEPER_CNT__;
                if (k1 !== k2) return k1 - k2;
                return v2.slotInfo.length - v1.slotInfo.length;
            });
        }
        sharedObj.emit('clear-status');
    }
    getFunctionBodyIndex(snapshotJSON) {
        var functionBodyIndex = [];
        for (let fb of snapshotJSON.funcBodies) {
            functionBodyIndex[fb.functionBodyId] = fb;
        }
        return functionBodyIndex;
    }
    getScopeArrayIndex(snapshotJSON) {
        var scopeArrayIndex = [];
        for (let scope of snapshotJSON.scopes) {
            scopeArrayIndex[scope.scopeId] = scope;
        }
        return scopeArrayIndex;
    }
    getSlotToScopeIndex(snapshotJSON) {
        var slotToScopeIndex = [];
        for (let scope of snapshotJSON.scopes) {
            if (!scope.subScopes) continue;
            for (let slot of scope.subScopes) {
                let slotId = slot.scopeId;
                if (slotToScopeIndex[slotId]) {
                    Utils.log('[!]: same slot used by different scopes');
                }
                slotToScopeIndex[slotId] = slotToScopeIndex[slotId] || [];
                slotToScopeIndex[slotId].push({scopeId: scope.scopeId});
            }
        }
        return slotToScopeIndex;
    }
    getSlotArrayIndex(snapshotJSON) {
        var slotArrayIndex = [];
        for (let slot of snapshotJSON.slotArrays) {
            slotArrayIndex[slot.slotId] = slot;
        }
        return slotArrayIndex;
    }
    /**
     * construct a map that keeps track of scopes and
     * function objects created within those scopes.
     * Those function objects keeps those scopes alive
     * since the scope will be used as closures when
     * the functions are executed.
     * @param {*} snapshotJSON 
     */
    getKeptAliveMap(snapshotJSON) {
        var map = [];
        for (let object of snapshotJSON.objects) {
            if (!object || !object.addInfo || !object.addInfo.scopeId) continue;
            let scopeId = object.addInfo.scopeId;
            if (!map[scopeId]) map[scopeId] = [];
            map[scopeId].push({
                objectId: object.objectId,
                functionBodyId: object.addInfo.functionBodyId,
            });
        }
        return map;
    }
    iterateSlotsInScope(scope, callback) {
        if (!scope) return;
        if (!scope.subScopes) return;
        for (let subScope of scope.subScopes) {
            let slot = this.querySlot(subScope.scopeId);
            if (!slot || !slot.slotInfo) {
                // otherwise this may be an activation object
                let object = this.queryObject(subScope.scopeId);
                if (!object.properties) continue;
                slot = {
                    slotId: object.objectId,
                    slotInfo: object.properties,
                };
            }
            callback(scope, slot);
        }
    }
    iterateObjectsInSlot(slot, callback) {
        if (!slot || !slot.slotInfo) return;
        for (let prop of slot.slotInfo) {
            if (!prop) continue;
            if (prop.entry && Utils.isArr(prop.entry)) {
                Utils.log('[!]: needs inspection [1]')
            } else if (prop.ptrIdVal) {
                callback(slot, prop.ptrIdVal, prop);
            }
        }
    }
    /**
     * construct a map that keeps track of each object 
     * and its enclosing scope information 
     */
    getEnclosingScopeMap(snapshotJSON) {
        var map = [];
        let iterateSlotCallback = (scope, slot, objectId, prop) => {
            if (!map[objectId]) map[objectId] = [];
            map[objectId].push({
                slotId: slot.slotId,
                scopeId: scope.scopeId,
                propIdInSlot: prop.propertyId,
            });
        };
        let iterateScopeCallback = (scope, slot) => {
            this.iterateObjectsInSlot(slot, iterateSlotCallback.bind(null, scope));
        };
        for (let scope of snapshotJSON.scopes) {
            this.iterateSlotsInScope(scope, iterateScopeCallback)
        }
        return map;
    }
    /**
     * propertyName to propertyId
     * @param {string} propName 
     */
    queryPropId(propName) {
        if (!this.propNameToPropId) return -1;
        return this.propNameToPropId.get(propName);
    }
    queryInternalPropId(propName) {
        if (!this.internalPropNameToPropId) return -1;
        return this.internalPropNameToPropId[propName];
    }
    queryFunctionBody(functionBodyId) {
        if (!this.functionBodyIndex) return null;
        return this.functionBodyIndex[functionBodyId];
    }
    queryScope(scopeId) {
        if (!this.scopeArrayIndex[scopeId]) return null;
        return this.scopeArrayIndex[scopeId];
    }
    querySlot(slotId) {
        if (!this.slotArrayIndex) return null;
        return this.slotArrayIndex[slotId];
    }
    /*getSourceInfoByFunctionBodyId(functionBodyId) {
        let functionBody = this.queryFunctionBody(functionBodyId);
        return {
            code: this.querySourceCodeByFileName(functionBody.filename),
            filename: functionBody.filename,
            line: functionBody.line,
            column: functionBody.column
        };
    }
    */
    /**
     * construct the points to relation between all objects
     * objectId -> Ids of all objects pointing to that object
     */
    getPointsToMap(snapshotJSON) {
        var map = [], _this = this;
        for (let obj of snapshotJSON.objects) {
            // get points to relations in objects
            if (obj.properties) {
                for (let prop of obj.properties) {
                    if (!prop) continue;
                    if (prop.entry && Utils.isArr(prop.entry)) {
                        Utils.log('[!]: needs inspection');
                    } else if (prop.ptrIdVal) {
                        if (!map[prop.ptrIdVal]) {
                            map[prop.ptrIdVal] = [];
                        }
                        map[prop.ptrIdVal].push({
                            propertyId: prop.propertyId,
                            parentObjId: obj.objectId,
                        });
                    }
                }
            }
            // get points to relations in arrays
            this.traverseArrayIndices(obj, (element, i) => {
                if (!element || !element.ptrIdVal) return;
                let id = element.ptrIdVal;
                if (!map[id]) {
                    map[id] = [];
                }
                map[id].push({
                    propertyId: -1,
                    arrayIdx: i,
                    parentObjId: obj.objectId,
                });
            })
            // if this is a typed array
            // and it has its internal buffer
            if (obj.addInfo &&
                obj.addInfo.objectId) {
                let id = obj.addInfo.objectId;
                if (!map[id]) {
                    map[id] = [];
                }
                map[id].push({
                    propertyId: _this.queryInternalPropId('<buffer>'),
                    parentObjId: obj.objectId,
                });
            }
            // if this is an object-array
            if (obj.objArrId) {
                let id = obj.objArrId;
                if (!map[id]) {
                    map[id] = [];
                }
                map[id].push({
                    propertyId: _this.queryInternalPropId('<object-array>'),
                    parentObjId: obj.objectId,
                });
            }
        }
        return map;
    }
    /**
     * @deprecated memory-consuming
     * @param {*} objectId 
     */
    getIndicesByObjectId(objectId) {
        let object = this.queryObject(objectId);
        return this.getIndices(object);
    }
    /**
     * @deprecated memory-consuming
     * @param {*} object 
     */
    getIndices(object) {
        if (!object || !object.addInfo || !object.addInfo.arrayInfo) {
            return null;
        }
        let ret = [];
        for (let i = 0; i < object.addInfo.arrayInfo.length; i++) {
            if (!object.addInfo.arrayInfo[i] || !object.addInfo.arrayInfo[i].arrIndices) continue;
            ret = ret.concat(object.addInfo.arrayInfo[i].arrIndices);
        }
        return ret;
    }
    getArrayLength(object) {
        if (!object || !object.addInfo) {
            return null;
        }
        if (object.addInfo.buffInfo) {
            return object.addInfo.length | 0;
        }
        if (object.addInfo.arrayInfo) {
            let ret = 0;
            for (let i = 0; i < object.addInfo.arrayInfo.length; i++) {
                if (!object.addInfo.arrayInfo[i] || !object.addInfo.arrayInfo[i].arrIndices) continue;
                let infoItem = object.addInfo.arrayInfo[i];
                ret += ((infoItem.offset - infoItem.index) | 0);
            }
            return ret;
        }
    }
    /**
     * traverse each one of the array elements
     * @param {*} objectId
     * @param {*} cb callback(element, global-index, indices-block, indices-block-index)
     */
    traverseArrayIndicesByObjectId(objectId, cb) {
        let object = this.queryObject(objectId);
        this.traverseArrayIndices(object, cb);
    }
    /**
     * traverse each one of the array elements
     * @param {*} object 
     * @param {*} cb callback(element, global-index, indices-block, indices-block-index)
     */
    traverseArrayIndices(object, cb) {
        if (!object || !object.addInfo || !object.addInfo.arrayInfo) {
            return null;
        }
        let idx = 0;
        for (let i = 0; i < object.addInfo.arrayInfo.length; i++) {
            if (!object.addInfo.arrayInfo[i] || !object.addInfo.arrayInfo[i].arrIndices) continue;
            let indices = object.addInfo.arrayInfo[i].arrIndices;
            for (let elem of indices) {
                cb(elem, idx++, indices, i);
            }
        }
    }
    queryScopesBySlotId(slotId) {
        if (!this.slotToScopeIndex) return [];
        return this.slotToScopeIndex[slotId] || [];
    }
    queryKeptAliveMapByScopeId(scopeId) {
        if (!this.keptAliveMap) return [];
        return this.keptAliveMap[scopeId];
    }
    querySourceCodeByFileId(fileId) {
        let filename = this.queryFileNameByFileId(fileId);
        return this.querySourceCodeByFileName(filename);
    }
    /**
     * query the file name from the fileId
     * @param {number} fileId 
     */
    queryFileNameByFileId(fileId) {
        if (!this.fileIdToFileNameMap) return '';
        return this.fileIdToFileNameMap[fileId];
    }
    /**
     * query the source code based on the filename
     * @param {string} filename 
     */
    querySourceCodeByFileName(filename) {
        if (!this.fileNameToSourceMap) return null;
        return this.fileNameToSourceMap[filename];
    }
    /**
     * query the object Id in the snapshot raw data
     * @param {*} objectId 
     */
    queryObject(objectId) {
        if (this.objectIndex[objectId]) return this.objectIndex[objectId];
        return `<obj ${objectId} not found>`;
    }

    /**
     * return well-known token of an object if exists 
     * @param {*} objectId 
     */
    queryObjectWellKnownToken(objectId) {
        if (
            this.objectIndex[objectId] &&
            this.objectIndex[objectId].wellKnownToken
        ) {
            return this.objectIndex[objectId].wellKnownToken;
        }
        return null;
    }

    /**
     * query the name of a property in the prop.json raw data
     * @param {*} propId 
     */
    queryPropName(propId) {
        if (this.propIndex[propId]) {
            /*
            if (!this.propIndex[propId].name) {
                return `<prop-id: ${propId}>`;
            }
            */
            return this.propIndex[propId].name;
        }
        return `<prop-id: ${propId}>`;
    }
    /**
     * query the type based on the type Id in the snapshot raw data
     * @param {*} typeId 
     */
    queryType(typeId) {
        if (this.typeIndex[typeId]) return this.typeIndex[typeId];
        return `<type ${typeId} not found>`;
    }
    /**
     * get one readable string that represenst the path from 
     * the root list to the object with the specified object id
     * @param {*} objectId 
     */
    getReadableName(objectId) {
        if (!this.keptAliveMap) return '';
        let ret = this.queryObjectWellKnownToken(objectId);
        if (ret) return ret;
        let paths = this.getTracesToRootList(objectId);
        if (!paths || paths.length <= 0) return '<unknown>';
        // return paths.map(v => this.getPathQualifiedName(v));
        return this.getPathQualifiedName(paths[0]);
    }
    getPathQualifiedName(path) {
        let rootName = this.queryObjectWellKnownToken(
            path[path.length - 1].objectId
        );
        rootName = rootName ? rootName : '<root>';
        let pathName =
            rootName +
            path
                .slice(1)
                .map(v => this.getTracePathItemName(v))
                .reverse()
                .join('');
        return pathName;
    }
    /**
     * 
     * @param {*} item an item on the tracing to root path
     * @return {string} the readable property of this item
     * in the fully qualified name
     */
    getTracePathItemName(item) {
        if (!item) return '<empty>';
        // let scopePropName = ' \u2282 <scope> \u2B38';
        let suffix = '';
        if (item.propertyId >= 0) {
            suffix = '.' + this.queryPropName(item.propertyId);
        } else if (item.arrayIdx >= 0) {
            suffix = '[' + item.arrayIdx + ']';
        } else if (item.scopeId >= 0) {
            let funcObject = this.queryObject(item.objectId);
            let funcName = funcObject.wellKnownToken
                ? funcObject.wellKnownToken
                : funcObject.addInfo.name;
            let propNameInSlot = this.queryPropName(item.propIdInSlot);
            // let closureFuncName = '';
            suffix = ` (${funcName}) \u290f` + ` <closure>.${propNameInSlot}`;
        }
        return suffix;
    }
    getJsTypeNameByObjectId(objectId) {
        if (!objectId) return '<unknown-type>';
        let object = this.queryObject(objectId);
        if (!object) return '<unknown-type>';
        let type = this.queryType(object.typeId);
        if (!type || !type.jsTypeId) return '<unknown-type>';
        return JSTypeInfo.getJSTypeName(type.jsTypeId);
    }
    /**
     * get the number of functions that keep a slot (closure) alive
     */
    getKeeperCountBySlotId(slotId) {
        let scopes = this.queryScopesBySlotId(slotId);
        let cnt = 0;
        if (scopes && scopes.length > 0) {
            for (let scopeItem of scopes) {
                let objects = this.queryKeptAliveMapByScopeId(
                    scopeItem.scopeId
                );
                if (!objects) continue;
                cnt += objects.length;
            }
        }
        return cnt;
    }
    /**
     * get a list of common property names 
     * referring to the object
     * @param {*} objectId 
     */
    getCommonNameByObjectId(objectId, stepMore = 0) {
        var parents, ret, closurePath;
        if (!objectId) return '';
        let jsType = this.getJsTypeNameByObjectId(objectId);
        // it doesn't make sense to infer the name of true or false
        if (jsType === 'Boolean') return '';
        // if this object is not referenced by any other objects
        if (!this.pointsToBy || !this.pointsToBy[objectId]) {
            // if kept alive by a closure
            if (this.enclosingScopeMap[objectId]) {
                let slotInfo = this.enclosingScopeMap[objectId];
                if (slotInfo && slotInfo[0])
                    return this.queryPropName(slotInfo[0].propIdInSlot);
            }

            // final guard checking
            if (this.rootListSet[objectId]) 
                return '<root>';
            else 
                return '<?>';
        }
        
        let freqs = {}, maxFreq = 0, parentObjId, isPropNumber = false;
        // if there are objects pointing to this object
        parents = this.pointsToBy[objectId];
        for (let pointer of parents) {
            if (!pointer) continue;
            let name = pointer.arrayIdx >= 0 ? 
                pointer.arrayIdx : this.queryPropName(pointer.propertyId);
            freqs[name] |= 0;
            if (++freqs[name] <= maxFreq) continue;
            maxFreq = freqs[name];
            ret = name;
            parentObjId = pointer.parentObjId;
            isPropNumber = pointer.arrayIdx >= 0;
        }

        let refinedName = this.refineCommonName(objectId, freqs, ret, isPropNumber, stepMore); 
        ret = refinedName.name;
        isPropNumber = refinedName.isPropNumber;
        stepMore = refinedName.stepMore;
        // if the most frequent name is a number
        // then we need to get the name of its parent too
        if (isPropNumber) {
            stepMore = stepMore <= 0 ? 1 : stepMore;
        }
        if (stepMore > 0) {
            let parentName = this.getCommonNameByObjectId(parentObjId, stepMore - 1);
            if (isPropNumber) ret = `${parentName}[${ret}]`;
            else ret = `${parentName}.${ret}`;
        }
        if (this.rootListSet[objectId]) ret = ret + ' (<root>)';
        return ret;
    }
    /**
     * do not prefer to use names such as 'constructor' or 'prototype'
     * @param {*} freqs 
     * @param {*} name 
     */
    refineCommonName(objectId, freqs, name, isPropNumber, stepMore) {
        if (!RtConfig.avoidNames[name]) {
            return { name, isPropNumber, stepMore };
        }
        let names = Utils.getOwnPropNames(freqs);
        let maxFreq = 0;
        // try to get the most frequent token that is not in the avoid list
        for (let token of names) {
            if (RtConfig.avoidNames[token]) continue;
            maxFreq = Math.max(maxFreq, freqs[token]);
            name = token;
            isPropNumber = Utils.parseInt(name) === name;
        }
        // if after filtering, the name is still
        // in the avoid list, try to use closure var name
        if (RtConfig.avoidNames[name] && this.enclosingScopeMap[objectId]) {
            // if kept alive by a closure
            let slotInfo = this.enclosingScopeMap[objectId];
            if (slotInfo && slotInfo[0]) {
                name = this.queryPropName(slotInfo[0].propIdInSlot);
                isPropNumber = false;
                stepMore = 0;
            }
        }
        return { name: name, 
                 isPropNumber: isPropNumber, 
                 stepMore: stepMore };
    }
    /**
     * get a list of common property names 
     * referring to the object
     * @param {*} objectId 
     */
    getParentObjects(objectId) {
        var parents, ret = [];
        if (!objectId) return ret;
        // if there are objects pointing to this object
        parents = this.pointsToBy[objectId] || [];
        for (let pointer of parents) {
            if (!pointer) continue;
            let name = pointer.arrayIdx >= 0 ? 
                pointer.arrayIdx : this.queryPropName(pointer.propertyId);
            ret.push({
                propName: name,
                parentObjId: pointer.parentObjId,
            });
        }
        // if keptalive by a enclosing scope
        if (this.enclosingScopeMap[objectId]) {
            let slotInfos = this.enclosingScopeMap[objectId];
            for (let slotInfo of slotInfos) {
                ret.push({
                    propName: this.queryPropName(slotInfo.propIdInSlot),
                    slotId: slotInfo.slotId,
                });
            }
        }
        return ret;
    }
    /**
     * a function that accepts an object's Id 
     * and returns a list of paths from that object
     * to the root list
     */
    getTracesToRootList(objectId) {
        let item,
            queue = [
                {
                    objectId: objectId,
                    path: [
                        {
                            objectId: objectId,
                            propertyId: -1,
                            arrayIdx: -1,
                        },
                    ],
                },
            ];
        // start searching for a trace from
        // an object to the root list
        let path, traversedSet = [], paths = [];
        let parentItem, parents;
        while (queue.length > 0) {
            item = queue.pop();
            if (traversedSet[item.objectId]) continue;
            traversedSet[item.objectId] = true;
            // Utils.log(item.path);
            // check if we have reached the root list
            if (this.rootListSet[item.objectId]) {
                paths.push(item.path);
                // stop searching when enough paths has been collected
                if (paths.length >= RtConfig.maxNumPath) break;
            }
            // search for objects that keep the
            // enclosing scope of this object alive
            if (this.enclosingScopeMap[item.objectId]) {
                let enclosingScopeItems = this.enclosingScopeMap[item.objectId];
                for (let scopeItem of enclosingScopeItems) {
                    let scopeId = scopeItem.scopeId;
                    if (!this.keptAliveMap[scopeId]) continue;
                    // avoid traversing the same scope twice
                    if (traversedSet[scopeId]) continue;
                    traversedSet[scopeId] = true;
                    let saverObjItems = this.keptAliveMap[scopeId];
                    for (let saverItem of saverObjItems) {
                        queue.push({
                            objectId: saverItem.objectId,
                            path: item.path.concat({
                                objectId: saverItem.objectId,
                                propertyId: -1,
                                arrayIdx: -1,
                                scopeId: scopeId,
                                slotId: scopeItem.slotId,
                                propIdInSlot: scopeItem.propIdInSlot,
                            }),
                        });
                    }
                }
            }
            // search for objects that directly
            // points to this object
            parents = this.pointsToBy[item.objectId];
            if (!parents) continue;
            for (parentItem of parents) {
                queue.push({
                    objectId: parentItem.parentObjId,
                    path: item.path.concat({
                        objectId: parentItem.parentObjId,
                        propertyId: parentItem.propertyId,
                        arrayIdx: parentItem.arrayIdx,
                    }),
                });
            }
        }
        return paths;
    }
    /**
     * a visitor function that traverses all the edges 
     * among objects in the memory graph
     * @param {*} cb 
     */
    traverse(cb) {
        if (!this.snapshotJSON.objects) {
            setTimeout(this.traverse.bind(null, cb), 100);
            return;
        }
        let _this = this;
        let iterateObjectInSlotCallback = (pointerObjectId, slot, objectId, prop) => {
            cb(pointerObjectId, objectId);
        }
        // first calculate the in-degrees and out-degrees
        this.snapshotJSON.objects.forEach(function (obj) {
            // traverse properties in an object
            if (obj.properties) {
                for (let prop of obj.properties) {
                    if (!prop) continue;
                    if (prop.entry) {
                        Utils.log('[!]: needs inspection [2]');
                    }
                    if (prop.ptrIdVal) {
                        cb(obj.objectId, prop.ptrIdVal);
                    }
                }
            }

            // traverse object to object-array
            if(obj.objArrId) {
                cb(obj.objectId, obj.objArrId);
            }

            // traverse indices in an array
            _this.traverseArrayIndices(obj, (element) => {
                if (!element || !element.ptrIdVal) return;
                cb(obj.objectId, element.ptrIdVal);
            });

            // traverse indirect edges, i.e., an object keeps 
            // a closure alive, which contains references to other objects
            /* if (obj.addInfo && obj.addInfo.scopeId) {
                let scope = _this.queryScope(obj.addInfo.scopeId);
                if (scope) {
                    _this.iterateSlotsInScope(scope, (scope, slot) => {
                        _this.iterateObjectsInSlot(slot,
                            iterateObjectInSlotCallback.bind(null, obj.objectId));
                    });
                }
            } */

            // traverse the path from a typed array to an array buffer
            if (obj.addInfo &&
                obj.addInfo.objectId) {
                cb(obj.objectId, obj.addInfo.objectId);
            }
        });
    }
}

if (!window.dataInterface) {
    window.dataInterface = new DataInterface();
}

export default window.dataInterface;



