/**
 * manages resouces loading from the server side
 * 
 * Author: Liang Gong
 */
import axios from 'axios';
import RtConfig from '../basic/Config.js';
import sharedObj from '../basic/Global.js';

let oboe = require('oboe');

/*
let request = require('request'),
    JSONStream = require('JSONStream'),
    _ = require('highland');
*/

if (!sharedObj.resourceLoader) {
    let resourceLoader = {
        server: RtConfig.server,
        port: RtConfig.port,
        cache: {},
        dataInfo: [
            { name: 'prop', path: `data/prop.json` },
            { name: 'allocTrace', path: `data/allocTracing_0.json` },
            { name: 'snapshot', path: `data/snap_1.json` },
        ],
    };
    /**
     * initialize the resource loader;
     */
    resourceLoader.init = function() {
        for (let info of this.dataInfo) {
            if (this.cache[info.name]) continue;
            this.cache[info.name] = {};
        }
    };
    resourceLoader.load = function() {
        let url;
        for (let info of this.dataInfo) {
            url = `http://${this.server}:${this.port}/${info.path}`;
            // url = `${process.env.PUBLIC_URL}/${info.path}`;
            if (info.name === 'none') {
                this.loadByOboe(url, info.name);
            } else {
                this.loadByAxios(url, info.name);
            }
        }
    };
    /**
     * loading the data through oboe
     */
    resourceLoader.loadByOboe = function(url, name) {
        let resCallback = data => {
            if (typeof data === 'string') {
                sharedObj.emit('show-status', `parsing ${name}...`);
                data = JSON.parse(data);
            }
            this.cache[name] = data;
            sharedObj.emitAlways(`load-${name}`, data);
        };

        sharedObj.emit('show-status', `loading ${name} from ${url}...`);
        oboe(url)//.done(resCallback);
    };
    /**
     * loading the data through request and JSONStream
     */
    /*
    resourceLoader.load_stream = function() {
        let url, data;
        for (let info of this.dataInfo) {
            let resCallback = data => {
                if (typeof data === 'string') {
                    sharedObj.emit('show-status', `parsing ${info.name}...`);
                    data = JSON.parse(data);
                }
                this.cache[info.name] = data;
                sharedObj.emitAlways(`load-${info.name}`, data);
            };
            url = `http://localhost:3000/${info.path}`;
            sharedObj.emit(
                'show-status',
                `loading ${info.name} from ${url}...`
            );
            _(request({ url }).pipe(JSONStream.parse())).map(resCallback);
        }
    };
    */

    /**
     * loading the data through axios
     */
    resourceLoader.loadByAxios = function(url, name) {
        let data;
        sharedObj.emit('show-status', `loading ${name} from ${url}...`);
        axios.get(url).then(res => {
            if (typeof res.data === 'string') {
                sharedObj.emit('show-status', `parsing ${name}...`);
                data = JSON.parse(res.data);
                // release the JSON string
                res.data = '';
            } else {
                data = res.data;
            }
            this.cache[name] = data;
            sharedObj.emitAlways(`load-${name}`, data);
        });
    };

    resourceLoader.init();
    sharedObj.resourceLoader = resourceLoader;
}

export default sharedObj.resourceLoader;



