import Utils from '../../basic/Utils.js';
import dataInterface from '../../data/DataInterface.js';

/**
 * detect sparse arrays
 */
class SparseArrayChecker {
    constructor() {
        this.sparseThreshold = 0.33;
    }
    check(object, treeNode) {
        // guard checking
        if (!Utils.HOP(object, 'objectId')) return;
        if (!Utils.isJsType(treeNode, 'Array')) return;
        if (!Utils.hasArrayInfo(treeNode)) return;
        // count the empty slots
        let emptySlotCnt = 0, totalSlotCnt = 0;
        let actualTotalSlotCnt = dataInterface.getArrayLength(object);
        dataInterface.traverseArrayIndices(object, (element) => {
            totalSlotCnt++;
            if (!element) emptySlotCnt++;
        });
        let estimateFlag = (totalSlotCnt === actualTotalSlotCnt) ? '' : '~';
        // set the flag in the UI tree node
        if (emptySlotCnt / totalSlotCnt >= this.sparseThreshold) {
            Utils.addIssue(treeNode, {
                type: 'sparse-array',
                name: `Sparse Array (${estimateFlag}${(100 * emptySlotCnt / totalSlotCnt).toFixed(2)}% empty)`,
            });
        }
    }
}

export default new SparseArrayChecker();



