/**
 * the main memory checker, which delegates the checking
 * to each checker extension in the "extensions" directory
 */
class MemoryChecker {
    constructor() {
        this.checkers = [];
        this.req = require.context(
            './extensions',
            true,
            /^(.*\.(js$))[^.]*$/igm
        );
        this.loadExtensions();
    }
    /**
     * load the extension checkers from the directory
     */
    loadExtensions() {
        let checkers = this.checkers;
        let req = this.req;
        this.req.keys().forEach(function(key) {
            console.log('loading ' + key + '...');
            checkers.push(req(key).default);
        });
    }
    checkMemIssues(object, treeNode) {
        var checker;
        for (let i = 0; i < this.checkers.length; i++) {
            checker = this.checkers[i];
            if (!checker || !checker.check) continue;
            checker.check(object, treeNode);
        }
    }
}

export default new MemoryChecker();



