/**
 * configuration of the app
 * 
 * Author: Liang Gong
 */
let RuntimeConfig = {
    // online version
    online: false,
    // server address
    server: 'localhost',
    // port number
    port: 'configed below',
    // debug mode show additional debug information in the visualization
    debugMode: process.env.NODE_ENV !== 'production',
    // the maximum number of paths to be returned
    maxNumPath: 20,
    // in the tree node, only show a substring if a string value is too long
    stringPreviewLen: 20,
    // the maximum number of chars to be displayed in the code editor
    editorSizeLimit: 200000,
    // the maximum number of items to visualize when expanding a list
    previewMaxSize: 15,
    // the interval size of incrementing the list size
    incrementInterval: 20,
    // if the memory graph show be visualized
    showMemGraph: false,
    // the interval for process the next bach of task
    // for rendering the memory graph
    memGraphRenderInterval: 33,
    // the # of edges is much bigger than the # of nodes
    renderEdges: false,
    // the height of the memory graph (px)
    graphHeight: 600,
    // configuration to prune the memory graph
    renderFullGraph: false,
    // the maximum number of nodes and links rerendered at a time
    // limit the upperbound to make the UI smooth
    maxNodesRendered: 128,
    maxLinksRendered: 64,
    machineType: 64, // 32 bit or 64 bit machine
    pointerSize: 8, // 8 bytes
    doubleSize: 8,
    intSize: 2,
    longSize: 4,
    // names to be given less priority when choosing common names
    avoidNames: ['constructor', 'prototype', '_super', 'super_'],
};

// configure the port number
if (RuntimeConfig.online) {
    RuntimeConfig.port = (process.env.NODE_ENV === 'production') ? 5001 : 3000;
} else {
    RuntimeConfig.port = (process.env.NODE_ENV === 'production') ? 5000 : 3000;
}

// convert the list into a hashtable
let names = RuntimeConfig.avoidNames || [];
let table = {};
for (let token of names) {
    table[token] = true;
}
RuntimeConfig.avoidNames = table;

if (RuntimeConfig.machineType === 64) {
    // on a 64 bit machine, a pointer uses 8 bytes
    RuntimeConfig.pointerSize = 8;
} else {
    // on a 32 bit machine, a pointer uses 4 bytes
    RuntimeConfig.pointerSize = 4;
}

export default RuntimeConfig;


