import JSTypeInfo from '../engine-info/JSType-Info.js';
import Utils from '../basic/Utils.js';
import RtConfig from '../basic/Config.js';
import dataInterface from '../data/DataInterface.js';
import sizeInfo from '../engine-info/ObjectSize.js';
import memChecker from '../checkers/mainChecker.js';

/**
 * TreeNodeTransformer transforms the raw JSON data into objects
 * that are better for representation
 */
class TreeNodeTransformer {
    constructor() {
        this.di = dataInterface;
    }
    /**
     * check if an object represents an allocation info in the JSON file
     * @param {*} obj 
     */
    isAllocationItem(obj) {
        return obj && obj.src && obj.src.line !== undefined;
    }
    /**
     * check if an object represents an item in the tracing root tree
     * @param {*} obj 
     */
    isTracingRootItem(obj) {
        return obj && !!obj.__TRACE_ROOT_ITEM__;
    }
    /**
     * check if an object represents an slot (closure)
     * @param {*} obj 
     */
    isSlotItem(obj) {
        return obj && !!obj.slotId;
    }
    /**
     * a function that accepts objects from the JSON parses
     * transforms it for better UI presentation
     * @param {*} obj objects from the JSON parser
     * @return objects for UI presentation
     */
    transform(obj) {
        if (this.isTracingRootItem(obj)) {
            return this.transformTraceRootItem(obj);
        } else if (this.isAllocationItem(obj)) {
            return this.transformAllocItem(obj);
        } else if (this.isSlotItem(obj)) {
            return this.transformSlotItem(obj);
        } else {
            let ret = this.transformObject(obj);
            memChecker.checkMemIssues(obj, ret);
            return ret;
        }
    }
    /**
     * a function that accepts objects from the trace 
     * root item raw representation object, and transforms 
     * it for better UI presentation
     * 
     * Particularly, this function transforms for UI that
     * represents an allocation site
     * 
     * @param {*} obj objects from the trace root item raw representation
     * @return objects for UI presentation
     */
    transformTraceRootItem(object) {
        let paths = this.di.getTracesToRootList(object.objectId);
        return this.transformTraceRootPaths(object, paths);
    }
    transformTraceRootPaths(object, paths) {
        if (!paths) return `<rootlist to ${object.objectId} not found>`;
        // code that calculate the root path item
        let ret = [];
        for (let path of paths) {
            if (!path) continue;
            ret.push(this.transformTraceRootPath(object, path));
        }
        return ret;
    }
    /**
     * a function that accepts objects from the trace 
     * root item raw representation object, and transforms 
     * it for better UI presentation
     * 
     * Particularly, this function transforms for UI that
     * represents a trace from an object to the root list
     * 
     * @param {*} obj objects from the trace root item raw representation
     * @return objects for UI presentation
     */
    transformTraceRootPath(object, path) {
        if (!path) return `<rootlist to ${object.objectId} not found>`;
        // code that calculate the root path item
        let qualifiedName = this.di.getPathQualifiedName(path);

        let ret = {};
        ret.__QUAL_NAME__ = qualifiedName;
        for (let i = 0; i < path.length; i++) {
            let suffix = '', item = path[i];
            if (!item) continue;
            suffix = this.di.getTracePathItemName(item);
            let icon = ' \u2191 '; // upper arrow
            if (i === 0) icon = ' \u2192 '; // middle dot
            let propName = i + icon + suffix;
            ret[propName] = { objectId: item.objectId };
        }
        return ret;
    }
    /**
     * a function that accepts objects from the JSON parses
     * transforms it for better UI presentation
     * 
     * Particularly, this function transforms for UI that
     * represents an allocation site information
     * 
     * @param {*} obj objects from the JSON parser
     * @return objects for UI presentation
     */
    transformAllocItem(obj) {
        // return obj;
        let ret = {}, meta = {};
        ret.__META__ = meta;
        meta.metaType = 'allocSite';
        // copy the prefix
        if (obj.__PREFIX__) {
            meta.__PREFIX__ = obj.__PREFIX__;
        }
        // copy source code info
        meta.line = obj.src.line;
        meta.column = obj.src.column;
        if (obj.src.function === 'Anonymous function') {
            meta.functionName = 'Anonymous';
        } else {
            meta.functionName = obj.src.function;
        }
        meta.filename = dataInterface.queryFileNameByFileId(obj.src.fileId);
        meta.source = true;
        // copy allocation info
        meta.count = obj.allocInfo.count;
        meta.estimatedSize = obj.allocInfo.estimatedSize;
        // copy allocted object info
        if (obj.objectIds) {
            for (let i = 0; i < obj.objectIds.length; i++) {
                let item = obj.objectIds[i];
                ret[i] = { objectId: item, __TRACE_ROOT__: true };
            }
        }
        // for each child calculate its shallow size
        if (obj.objectIds) {
            meta.__ALLOC_SIZE_INFO__ = sizeInfo.sizeOfAllocationSite(obj);
        }
        // copy subpaths into this object
        if (obj.subPaths) {
            for (let i = 0; i < obj.subPaths.length; i++) {
                let item = obj.subPaths[i];
                ret[`path-${i}`] = item;
                item.__PREFIX__ = 'called at ';
            }
        }
        return ret;
    }
    transformSlotItem(obj) {
        if (!obj || !obj.slotId) return obj;
        let ret = Array.isArray(obj) ? [] : {};
        let id = obj.slotId;
        let objBody = this.di.querySlot(id);
        // if the object is not found, return the message
        if (typeof objBody === 'string') return objBody;

        // copy the real slot properties
        let propCnt = 0;
        if (objBody.slotInfo) {
            let i = 0;
            for (let item of objBody.slotInfo) {
                if (item === null) continue;
                let name = this.di.queryPropName(item.propertyId);
                if (item.entry) {
                    Utils.log('[!]: needs inspection [3]');
                }
                propCnt++;
                if (item.ptrIdVal) {
                    ret[name] = this.di.queryObject(item.ptrIdVal);
                } else if (item.i32Val !== undefined) {
                    ret[name] = item.i32Val;
                } else if (item.doubleVal !== undefined) {
                    ret[name] = item.doubleVal;
                } else {
                    Utils.log(item);
                }
            }
        }

        ret.__META__ = ret.__META__ ? ret.__META__ : {};
        // copy the properties in the object's meta object
        // into the __META__ section in the UI
        for (let prop in obj) {
            if (!Utils.HOP(obj, prop)) continue;
            if (prop === 'slotId') continue;
            ret.__META__[prop] = obj[prop];
        }

        // copy the objBody's meta info into the __META__ section
        for (let prop in objBody) {
            if (!Utils.HOP(objBody, prop)) continue;
            if (prop === 'slotId') continue;
            if (prop === 'slotInfo') continue;
            ret.__META__[prop] = objBody[prop];
        }

        // get size information
        ret.__META__.__SIZE_INFO__ = RtConfig.pointerSize * propCnt;
        ret.__META__.__CLOSURE_VAR_CNT__ = propCnt;

        ret.__META__.__SLOT_ITEM__ = true;

        if (objBody.functionBodyId) {
            // get function body information and copy into the slot item
            let fb = dataInterface.queryFunctionBody(objBody.functionBodyId);
            if (fb) {
                ret.__META__.functionName = fb.name;
                // copy filename, line, column into the meta object
                ret.__META__.filename = dataInterface.queryFileNameByFileId(fb.fileId);
                ret.__META__.line = fb.line;
                ret.__META__.column = fb.column;
            }
        }

        // get points to relation
        let itemId = 0;
        let scopes = dataInterface.queryScopesBySlotId(id);
        if (scopes && scopes.length > 0) {
            ret.__POINTS_TO_BY__ = {};
            for (let scopeItem of scopes) {
                let objects = dataInterface.queryKeptAliveMapByScopeId(
                    scopeItem.scopeId
                );
                if (!objects) continue;
                for (let objectItem of objects) {
                    ret.__POINTS_TO_BY__[itemId++] = {
                        objectId: objectItem.objectId,
                        __META__: {
                            __IS_POINTS_TO_ITEM__: true,
                        },
                    };
                }
            }
        }

        ret.__META__.__KEEPER_CNT__ = itemId;
        return ret;
    }
    /**
     * a function that accepts objects from the JSON parses
     * transforms it for better UI presentation
     * 
     * Particularly, this function transforms for UI that
     * represents an JavaScript object/array/primitive value
     * 
     * @param {*} obj objects from the JSON parser
     * @return objects for UI presentation
     */
    transformObject(obj) {
        if (typeof obj !== 'object' && typeof obj !== 'function') return obj;
        let ret = Array.isArray(obj) ? [] : {};
        let id = obj.objectId || obj.primitiveId;
        if (!id) {
            // TODO: extract the filtering functionality
            for (let prop in obj) {
                if (!Utils.HOP(obj, prop)) continue;
                if (prop === 'runtime') continue;
                if (prop === 'contexts') continue;
                if (prop === 'funcBodies') continue;
                if (prop === 'objectTypes') continue;
                if (prop === 'objectHandlers') continue;
                if (prop === 'scopes') continue;
                if (prop === 'symbolTable') continue;
                if (prop === 'ctxTag') continue;
                if (prop === 'timeWrite') continue;
                ret[prop] = obj[prop];
            }
            return ret;
        }

        let objBody = this.di.queryObject(id);
        // if the object is not found, return the message
        if (typeof objBody === 'string') {
            return objBody;
        }

        // get trace for all objects
        ret.__TRACE__ = {
            objectId: id,
            __TRACE_ROOT_ITEM__: true,
        };

        // if there is no need to trace root
        // if (!ret.__META__.__TRACE_ROOT__) {
        //     delete ret.__TRACE__;
        // } else {
        //     ret.__TRACE__ = {
        //       objectId: id,
        //       __TRACE_ROOT_ITEM__: true,
        //    };
        // }

        // if the object has the objArrId field, which means this object's
        // inner representation is actually an array
        if (objBody.objArrId) {
            ret['<object-array>'] = { objectId: objBody.objArrId };
        }

        // copy the real object properties
        if (objBody.properties) {
            let i = 0;
            for (let item of objBody.properties) {
                if (item === null) continue;
                let name = this.di.queryPropName(item.propertyId);
                if (item.entry) {
                    Utils.log('[!]: needs inspection [3]');
                }
                if (item.ptrIdVal) {
                    ret[name] = this.di.queryObject(item.ptrIdVal);
                } else if (item.i32Val !== undefined) {
                    ret[name] = item.i32Val;
                } else if (item.doubleVal !== undefined) {
                    ret[name] = item.doubleVal;
                } else {
                    Utils.log(item);
                }
            }
        }
        // if it's an array, copy all array properties
        if (objBody.addInfo && objBody.addInfo.arrayInfo) {
            this.di.traverseArrayIndicesByObjectId(id, (element, i) => {
                if (!element) return;
                if (element.ptrIdVal) {
                    ret[i] = { objectId: element.ptrIdVal };
                } else {
                    let names = Utils.getOwnPropNames(element);
                    let cnt = 0, valPropName;
                    for (let name of names) {
                        if (name.lastIndexOf('Val') + 3 === name.length) {
                            valPropName = name;
                            cnt++;
                        }
                        if (cnt > 1) break;
                    }
                    if (cnt !== 1) {
                        // if more than one prop ends with 'Val'
                        ret[i] = JSON.parse(Utils.stringify(element));
                    } else {
                        // else copy its value directly
                        ret[i] = element[valPropName];
                    }
                }
            });
        }

        // now deal with the type information
        if (objBody.typeId) {
            let type = this.di.queryType(objBody.typeId);
            // get its prototype object
            if (type.prototypeVar && type.prototypeVar.ptrIdVal) {
                // copy a dummy object, which will be transformed later
                ret.__PROTO__ = { objectId: type.prototypeVar.ptrIdVal };
            }
            // copy other meta properties into the __META__ section
            ret.__META__ = {};
            for (let prop in type) {
                if (!Utils.HOP(type, prop)) continue;
                if (prop === 'typeId') continue;
                // if (prop === 'prototypeVar') continue;
                ret.__META__[prop] = type[prop];
                if (prop === 'jsTypeId') {
                    ret.__META__.jsTypeName = JSTypeInfo.getJSTypeName(
                        type[prop]
                    );
                }
            }
        }
        ret.__META__ = ret.__META__ ? ret.__META__ : {};
        // copy the properties in the object's meta object
        // into the __META__ section in the UI
        for (let prop in obj) {
            if (!Utils.HOP(obj, prop)) continue;
            if (prop === 'objectId') continue;
            if (prop === 'primitiveId') continue;
            ret.__META__[prop] = obj[prop];
        }

        // copy the objBody's meta info into the __META__ section
        for (let prop in objBody) {
            if (!Utils.HOP(objBody, prop)) continue;
            if (prop === 'objectId') continue;
            if (prop === 'primitiveId') continue;
            if (prop === 'properties') continue;
            ret.__META__[prop] = objBody[prop];
        }

        // get size information
        ret.__META__.__SIZE_INFO__ = sizeInfo.sizeof(id);

        // if function object, get function body info
        if (objBody.addInfo && objBody.addInfo.functionBodyId) {
            let fbId = objBody.addInfo.functionBodyId;
            let fb = dataInterface.queryFunctionBody(fbId);
            if (fb) {
                ret.__META__.__FUNC_BODY__ = true;
                ret.__META__.functionBodyId = fbId;
                ret.__META__.filename = dataInterface.queryFileNameByFileId(fb.fileId);
                ret.__META__.line = fb.line;
                ret.__META__.column = fb.column;
            }
        }

        // calculate common name
        ret.__META__.__COMMON_NAME__ = 
            dataInterface.getCommonNameByObjectId(id, 2);
        Utils.ensureConciseCommonName(ret);

        // get points to relation
        let parents = dataInterface.getParentObjects(id);
        if (parents && parents.length > 0) {
            ret.__POINTS_TO_BY__ = {};
            for (let parent of parents) {
                ret.__POINTS_TO_BY__[parent.propName] = {
                    objectId: parent.parentObjId,
                    __META__: {
                        __IS_POINTS_TO_ITEM__: true,
                    },
                };
            }
        }
        return ret;
    }
}

export default TreeNodeTransformer;



