/**
 * manages the shared information in the web page
 * 
 * Author: Liang Gong
 */
if (!global.storage) {
    global.storage = {};

    /**
     * event map keeps track of mapping from
     * unemitted event names to their parameters
     */
    let eventMap = {};
    let eventAlwaysMap = {};

    /**
     * 
     * callback map keeps track of mapping from
     * event names to callback functions
     */
    let callbackMap = {};
    let callbackOnceMap = {};

    /**
     * register an event with a callback
     */
    global.storage.register = function(prop, cb) {
        if (!callbackMap[prop]) {
            callbackMap[prop] = [];
        }
        callbackMap[prop].push(cb);
        // emit the queued events to the callback registered
        if (!eventMap[prop]) eventMap[prop] = [];
        while(eventMap[prop].length > 0) {
            setTimeout(cb.bind(null, eventMap[prop].shift()), 0);
        }
    };

    /**
     * register an event with a callback
     */
    global.storage.once = function(prop, cb) {
        if (!callbackOnceMap[prop]) {
            callbackOnceMap[prop] = [];
        }
        let called = false;
        // emit the queued events to the callback registered
        if (!eventMap[prop]) eventMap[prop] = [];
        if (!eventAlwaysMap[prop]) eventAlwaysMap[prop] = [];
        // emit the events that persists
        for (let obj of eventAlwaysMap[prop]) {
            setTimeout(cb.bind(null, obj), 0);
            called = true;
        }
        // emit the other normal events
        while(eventMap[prop].length > 0) {
            setTimeout(cb.bind(null, eventMap[prop].shift()), 0);
            called = true;
        }
        
        if (!called) {
            callbackOnceMap[prop].push(cb);
        }
    };

    /**
     * emit an event with a parameter object
     */
    global.storage.emit = function(prop, obj) {
        if (!callbackMap[prop]) {
            // queue the event and return
            if (!eventMap[prop]) eventMap[prop] = [];
            eventMap[prop].push(obj);
            return;
        }
        // event each registered callback with the event
        for (let cb of callbackMap[prop]) {
            setTimeout(cb.bind(null, obj), 0);
        }
        while(callbackOnceMap[prop] && callbackOnceMap[prop].length > 0) {
            let cb = callbackOnceMap[prop].shift();
            setTimeout(cb.bind(null, obj), 0);
        }
    };

    /**
     * emit an event with a parameter object
     */
    global.storage.emitAlways = function(prop, obj) {
        if (!eventAlwaysMap[prop]) {
            eventAlwaysMap[prop] = [];
        }
        if (!callbackMap[prop]) {
            callbackMap[prop] = [];
        }
        if (!callbackOnceMap[prop]) {
            callbackOnceMap[prop] = [];
        }
        eventAlwaysMap[prop].push(obj);
        // event each registered callback with the event
        for (let cb of callbackMap[prop]) {
            setTimeout(cb.bind(null, obj), 0);
        }
        while(callbackOnceMap[prop].length > 0) {
            let cb = callbackOnceMap[prop].shift();
            setTimeout(cb.bind(null, obj), 0);
        }
    };
}

export default global.storage;



