import React from 'react';
import ReactDOM from 'react-dom';
import Utils from '../basic/Utils.js';
import TreeView from 'react-treeview';
import RtConfig from '../basic/Config';
import AlertIcon from './icons/AlertIcon.js';
import InfoIcon from './icons/InfoIcon.js';

class TagGenerator {
    constructor() {
        this.fileSplitRegex = /[/\\]/;
    }
    clear(arr) {
        if (!arr) return;
        while (arr.length > 0) {
            arr.pop();
        }
    }
    captalize(str) {
        if (!str) return str;
        if (typeof str !== 'string') return str;
        if (str.length <= 0) return str;
        return str[0].toUpperCase() + str.substring(1);
    }
    getPreviewIfLongString(val) {
        if (typeof val === 'string') {
            // only show a substring if the string is too long
            if (val.length > RtConfig.stringPreviewLen) {
                val = val.substring(0, RtConfig.stringPreviewLen) + '...';
            }
        }
        return val;
    }
    getPrimitiveTreeNodeValueTag(args) {
        let { treeNode } = args;
        // treeNode = this.getPreviewIfLongString(treeNode);
        let enclose = typeof obj === 'string' ? "'" : '';
        let displayValue = (
            <span>
                {enclose}
                <span className={'prim-value ' + (typeof treeNode + '-value')}>
                    {treeNode + ''}
                </span>
                {enclose}
            </span>
        );
        return displayValue;
    }
    // TODO: refactor this function
    getTreeNodeSubComponents(args) {
        var {
            id,
            treeNode,
            label,
            path,
            parent,
            fold,
            state,
            props,
            TreeComponent,
        } = args;
        // start rendering the sub-componenets of this UI component
        let subComponents = [];
        // initialize some values
        let treeNodeProps = Utils.getOwnPropNames(treeNode);
        let numProps = treeNodeProps.length;
        // initialize the children items' visualization range
        if (!state.visRange[id]) {
            state.visRange[id] = [0, Math.min(numProps - 1,
                RtConfig.incrementInterval - 1)];
        }
        let expandAll = function () { 
            state.visRange[id] = [0, numProps - 1]
            parent.setState({state}); 
        };
        let expandIncrement = function () {
            let next = Math.min(numProps - 1,
                state.visRange[id][1] + RtConfig.incrementInterval);
            state.visRange[id] = [state.visRange[id][0], next];
            parent.setState({state}); 
        };
        let expandShift = function(offset = 0) {
            let start = state.visRange[id][0];
            let end = state.visRange[id][1];
            if (start + offset >= 0 && start + offset <= numProps - 1) {
                start = Math.max(0, start + offset);
                end = end + offset;
            }
            state.visRange[id] = [start, end];
            parent.setState({state}); 
        }
        // initialize the range
        let [rangeStart, rangeEnd] = state.visRange[id];
        if (!state.isUnfolded[id]) this.clear(subComponents);
        if (subComponents.length <= 0 && state.isUnfolded[id]) {
            let i;
            if (rangeStart > 0) {
                // add the ... symbol before the preview
                subComponents.push(
                    <div className="info">
                        <span>
                            ...
                        </span>
                    </div>
                )
            }
            for (i = rangeStart; i <= rangeEnd && i < numProps; i++) {
                let prop = treeNodeProps[i];
                if (!RtConfig.debugMode && prop === '__META__') continue;
                subComponents.push(
                    <div className="info">
                        <TreeComponent
                            path={path.concat(prop).join('|')}
                            mainEntry={props.mainEntry}
                            componentId={state.componentId}
                        />
                    </div>
                );
            }
            if (i < numProps - 1) {
                // add the ... symbol after the preview
                subComponents.push(
                    <div className="info">
                        <span>
                            ...
                        </span>
                    </div>
                )
            }
            if (i < numProps - 1 || rangeStart > 0) {
                // add expand buttons
                subComponents.push(
                    <div className="info">
                        <span className="expand-button" onClick={expandAll}>
                            [<span className="expand-content">all
                                (<span className="expand-content-num">{numProps}</span>)
                             </span>]
                        </span>
                        <span className="expand-button" onClick={expandIncrement}>
                            [<span className="expand-content">{RtConfig.incrementInterval} more</span>]
                        </span>
                        <span className="expand-button" onClick={expandShift.bind(null, -1 * RtConfig.incrementInterval)}>
                            [<span className="expand-content">{RtConfig.incrementInterval} left</span>]
                        </span>
                        <span className="expand-button" onClick={expandShift.bind(null, RtConfig.incrementInterval)}>
                            [<span className="expand-content">{RtConfig.incrementInterval} right</span>]
                        </span>
                    </div>
                )
            }
            let metaPropIdx = treeNodeProps.indexOf('__META__');
            let tracePropIdx = treeNodeProps.indexOf('__TRACE__');
            let pointsToByPropIdx = treeNodeProps.indexOf('__POINTS_TO_BY__');
            // if meta node is not displayed, add it
            if (RtConfig.debugMode && treeNode.__META__ && 
                metaPropIdx >=0 && (metaPropIdx < rangeStart || metaPropIdx > rangeEnd)) {
                let prop = '__META__';
                subComponents.push(
                    <div className="info">
                        <TreeComponent
                            path={path.concat(prop).join('|')}
                            mainEntry={props.mainEntry}
                            componentId={state.componentId}
                        />
                    </div>
                );
            }
            // if trace node is not displayed, add it
            if (treeNode.__TRACE__ && 
                tracePropIdx >=0 && (tracePropIdx < rangeStart || tracePropIdx > rangeEnd)) {
                let prop = '__TRACE__';
                subComponents.push(
                    <div className="info">
                        <TreeComponent
                            path={path.concat(prop).join('|')}
                            mainEntry={props.mainEntry}
                            componentId={state.componentId}
                        />
                    </div>
                );
            }
            // if pointsToBy relation is not displayed, add it
            if (treeNode.__POINTS_TO_BY__ && 
                pointsToByPropIdx >=0 && (pointsToByPropIdx < rangeStart || pointsToByPropIdx > rangeEnd)) {
                let prop = '__POINTS_TO_BY__';
                subComponents.push(
                    <div className="info">
                        <TreeComponent
                            path={path.concat(prop).join('|')}
                            mainEntry={props.mainEntry}
                            componentId={state.componentId}
                        />
                    </div>
                );
            }
        }
        return subComponents;
    }
    getDisplayTagForPrimitiveNodeInTree(args) {
        let {
            treeNode,
            label,
            clickHandler,
            sizeTag,
            jsTypeTag,
            displayValue,
            displayType,
        } = args;
        let disTag = (
            <span className="node" onClick={clickHandler}>
                <span className={'prop-name ' + (typeof treeNode + '-prop')}>
                    {label}
                </span>
                {' '}
                {jsTypeTag}
                {' '}
                {displayValue}
                {' '}
                {displayType}
                {' '}
                <wbr />
                {' '}
                {sizeTag}
            </span>
        );
        return disTag;
    }
    getDisplayTagForArrayNodeInTree(args) {
        let { treeNode, label, clickHandler, sizeTag } = args;
        let className = (label === '__TRACE__' ? 'meta-prop-name' : 'prop-name'); 
        let disTag = (
            <span className="node area" onClick={clickHandler}>
                <span className={className}>{label}</span> <wbr /> {sizeTag}
            </span>
        );
        return disTag;
    }
    /**
     * generated the HTML tag that displays the main entry of this object
     * @param {*} treeNode 
     * @param {*} label 
     * @param {*} args 
     */
    getDisplayTagForObjectNodeInTree(treeNode, label, args) {
        let disTag = '';
        if (treeNode && treeNode.__META__ && treeNode.__META__.__SLOT_ITEM__) {
            // generate the tag for tree nodes that represents slot items
            let meta = treeNode.__META__;
            let functionTag = (
                <span className="function-name">{meta.functionName}</span>
            );
            disTag = (
                <span
                    className="node"
                    onClick={args.clickCallback}
                    onMouseOver={args.mouseOverCallback}
                >
                    <span className="prop-name">{label}</span>
                    {' '}
                    closure in &lt;
                    {functionTag}
                    &gt;
                    {' '}
                    <wbr />
                    {' '}
                    {args.sizeTag}
                </span>
            );
        } else if (treeNode && treeNode.__META__ && treeNode.__META__.source) {
            // generate the tag for tree nodes that represents allocation sites
            let meta = treeNode.__META__;
            let functionTag = (
                <span className="function-name">{meta.functionName}</span>
            );
            let filenameTag = (
                <span className="file-name">
                    {meta.filename.split(this.fileSplitRegex).pop()}
                </span>
            );
            let positionTag = (
                <span>
                    <span className="line">{meta.line}</span>
                    :
                    <span className="column">{meta.column}</span>
                </span>
            );
            disTag = (
                <span
                    className="node"
                    onClick={args.clickCallback}
                    onMouseOver={args.mouseOverCallback}
                >
                    <span className="prop-name">{label}</span>
                    {' '}
                    {args.prefixTag}
                    {' '}
                    {filenameTag}
                    {' '}
                    (
                    {positionTag}
                    )
                    {' '}
                    <wbr />
                    {' '}
                    in &lt;
                    {functionTag}
                    &gt;
                    {' '}
                    <wbr />
                    {' '}
                    {args.sizeTag}
                </span>
            );
        } else if (label !== '__META__' && label !== '__TRACE__' && label !== '__POINTS_TO_BY__') {
            // generate the tag for tree nodes that represents JavaScript
            // memory items (e.g., objects, functions, primitive values)
            disTag = (
                <span
                    className="node"
                    onClick={args.clickCallback}
                    onMouseOver={args.mouseOverCallback}
                >
                    <span className="prop-name">{label}</span>
                    {' '}
                    {args.jsTypeTag}
                    {' '}
                    {args.displayType}
                    {' '}
                    <wbr />
                    {' '}
                    {args.sizeTag}
                    {' '}
                    {args.alertTag}
                    {args.infoTag}
                    <wbr />
                    {' '}
                    <span className="well-known-token">
                        {args.wellKnownTokenTag}
                    </span>
                    <wbr />
                    {' '}
                    <span className="prim-obj-val">
                        {args.primitiveObjectValueTag}
                    </span>
                </span>
            );
        } else {
            // generate the tag for tree nodes that represents meta information
            disTag = (
                <span
                    className="node"
                    onClick={args.clickCallback}
                    onMouseOver={args.mouseOverCallback}
                >
                    <span className="meta-prop-name">{label}</span>
                    {' '}
                    <wbr />
                    {' '}
                    {args.jsTypeTag}
                </span>
            );
        }
        return disTag;
    }
    /**
     * return a tag for displaying the well known token
     * @param {*} treeNode tree node in UI 
     */
    getWellKnownTokenTag(treeNode) {
        let wellKnownTokenTag = '';
        if (treeNode.__QUAL_NAME__) {
            wellKnownTokenTag = treeNode.__QUAL_NAME__.replace(
                /\./g,
                '\u200B.' // \u200B is the zero width space
            );
        } else if (
            treeNode && treeNode.__META__ && treeNode.__META__.wellKnownToken
        ) {
            wellKnownTokenTag = treeNode.__META__.wellKnownToken.replace(
                /\./g,
                '\u200B.' // \u200B is the zero width space
            );
        } else if (
            treeNode && treeNode.__META__ && treeNode.__META__.__COMMON_NAME__
        ) {
            wellKnownTokenTag = (<span className="common-name">{treeNode.__META__.__COMMON_NAME__}</span>);
        }
        return wellKnownTokenTag;
    }
    /**
     * return a tag for displaying the primitive values
     * @param {*} treeNode tree node in UI 
     */
    getPrimitiveObjectValueTag(treeNode) {
        let primitiveObjectValueTag = '';
        if (treeNode && treeNode.__META__) {
            if (treeNode.__META__.stringVal) {
                primitiveObjectValueTag =
                    "'" + this.getPreviewIfLongString(treeNode.__META__.stringVal) + "'";
            } else if (treeNode.__META__.boolVal !== undefined) {
                primitiveObjectValueTag = treeNode.__META__.boolVal + '';
            }
        }
        return primitiveObjectValueTag;
    }
    /**
     * return a tag for displaying the type of primitive values in the engine
     * @param {*} treeNode tree node in UI 
     */
    getDisplayTypeTag(treeNode) {
        let displayType = '';
        /*
        if (treeNode && treeNode.__META__) {
            if (treeNode.__META__.stringVal) {
                displayType = (
                    <span>[<span className="type">prim-obj</span>]</span>
                );
            } else if (treeNode.__META__.boolVal !== undefined) {
                displayType = (
                    <span>[<span className="type">prim-obj</span>]</span>
                );
            }
        }*/
        return displayType;
    }
    /**
     * return a tag for displaying the JavaScript type of objects
     * @param {*} treeNode tree node in UI 
     */
    getJsTypeTag(treeNode) {
        let jsTypeTag = '';
        if (treeNode && treeNode.__META__ && treeNode.__META__.jsTypeName) {
            jsTypeTag = (
                <span>
                    : &lt;
                    <span className="jsType">
                        {treeNode.__META__.jsTypeName}
                    </span>
                    &gt;
                </span>
            );
        }
        return jsTypeTag;
    }
    /**
     * return a tag for displaying the JavaScript type of primitive values
     * @param {*} treeNode tree node in UI 
     */
    getPrimitiveJsTypeTag(treeNode) {
        let jsTypeTag = '';
        jsTypeTag = (
            <span>
                : &lt;
                <span className="jsType">
                    {this.captalize(typeof treeNode)}
                </span>
                &gt;
            </span>
        );
        return jsTypeTag;
    }
    /**
     * return a tag for displaying a prefix info if exists
     * @param {*} treeNode tree node in UI 
     */
    getPrefixTag(treeNode) {
        let prefixTag = '';
        if (treeNode && treeNode.__META__ && treeNode.__META__.__PREFIX__) {
            prefixTag = (
                <span className="item-prefix">
                    {treeNode.__META__.__PREFIX__}
                </span>
            );
        }
        return prefixTag;
    }
    /**
     * return a tag for displaying the size of an object
     * @param {*} treeNode tree node in UI 
     */
    getSizeTag(treeNode) {
        let sizeTag = '';
        if (!treeNode || !treeNode.__META__) {
            return sizeTag;
        }
        if (
            treeNode.__META__.__SLOT_ITEM__
        ) {
            sizeTag = (
                <span>
                    {'\u301A'}
                    <span className="size-info">
                        {Utils.readableSize(treeNode.__META__.__SIZE_INFO__)}
                    </span>
                    :
                    <span className="size-info">
                        {Utils.readableSize(treeNode.__META__.__KEEPER_CNT__)}
                    </span>
                    {'\u301B'}
                </span>
            );
        } else if (
            treeNode.__META__.__SIZE_INFO__ !== undefined
        ) {
            sizeTag = (
                <span>
                    {'\u301A'}
                    <span className="size-info">
                        {Utils.readableSize(treeNode.__META__.__SIZE_INFO__)}
                    </span>
                    {'\u301B'}
                </span>
            );
        } else if (
            treeNode.__META__.metaType === 'allocSite' &&
            (treeNode.__META__.__ALLOC_SIZE_INFO__ || 
             treeNode.__META__.estimatedSize)
        ) {
            let size, meta = treeNode.__META__;
            if(meta.__ALLOC_SIZE_INFO__)
                size = Utils.readableSize(meta.__ALLOC_SIZE_INFO__)
            else
                size = "~" + Utils.readableSize(meta.estimatedSize);
            sizeTag = (
                <span>
                    {'\u301A'}
                    <span className="size-info">
                        {size}
                    </span>
                    {'\u301B'}
                </span>
            );
        }
        return sizeTag;
    }
    /**
     * return a tag for displaying the alert icon
     * @param {*} treeNode tree node in UI 
     */
    getAlertTag(treeNode) {
        return AlertIcon.getTag(treeNode);
    }
    /**
     * return a tag for displaying the alert icon
     * @param {*} treeNode tree node in UI 
     */
    getInfoTag(treeNode) {
        return InfoIcon.getTag(treeNode);
    }
    /**
     * get the tree view tag
     */
    getTreeViewTag(
        id,
        disTag,
        collapsed,
        subComponents,
        clickCallback,
        mouseOverCallback
    ) {
        return (
            <TreeView
                key={id}
                nodeLabel={disTag}
                collapsed={collapsed}
                onClick={clickCallback}
                onMouseOver={mouseOverCallback}
            >
                {subComponents}
            </TreeView>
        );
    }
}

export default new TagGenerator();



