import React from 'react';
import ReactTooltip from 'react-tooltip';
import infoIcon from 'react-icons/lib/md/info.js';

class InfoIconTag extends React.Component {
    componentDidMount() {
        ReactTooltip.rebuild();
    }
    render() {
        return (
            <span data-tip={this.props.tooltipMsg}>
                {React.createElement(infoIcon, {
                    color: 'yellow',
                    size: 14,
                })}
            </span>
        );
    }
}

class InfoIcon {
    constructor() {}
    getTag(treeNode) {
        if (!treeNode || !treeNode.__META__ || !treeNode.__META__.__INFOS__) {
            return '';
        }
        return <InfoIconTag tooltipMsg={treeNode.__META__.__INFOS__[0].name} />;
    }
}

export default new InfoIcon();



