import JSTypeInfo from '../engine-info/JSType-Info.js';
import Utils from '../basic/Utils.js';
import RtConfig from '../basic/Config.js';
import dataInterface from '../data/DataInterface.js';

let propertyPointerSize = JSTypeInfo.getObjectPropertyPointerSize();
let arrayIndexPointerSize = JSTypeInfo.getArrayIndexPointerSize();
/**
 * calculate the object size based on its type information and structure
 */
class SizeInfo {
    constructor() {}
    /**
     * calculate the shallow size of an object
     * @param {*} id the id could be an objectId or a primitiveId
     */
    sizeof(id) {
        let object = dataInterface.queryObject(id);
        if (!object) {
            return '<unknown size>';
        } else {
            return this.sizeofObject(object);
        }
    }
    sizeOfAllocationSite(object) {
        if (!object || !object.objectIds) return 0;
        // for each child calculate its shallow size
        let size = 0;
        for (let i = 0; i < object.objectIds.length; i++) {
            size += this.sizeof(object.objectIds[i]);
        }
        return size;
    }
    sizeofObject(object) {
        if (!object || !object.typeId) return 0;
        let type = dataInterface.queryType(object.typeId);
        if (type.jsTypeId === undefined) return -1;
        let overhead = JSTypeInfo.getJSTypeOverhead(type.jsTypeId);
        let bodySize = this.getContentSize(object, type.jsTypeId);
        return overhead + bodySize;
    }
    getContentSize(object, jsTypeId) {
        let ret = 0;
        if (JSTypeInfo.getJSTypeName(jsTypeId) === 'String') {
            ret += this.getStringContentSize(object);
        } 
        if (object && object.properties) {
            ret += this.getObjectContentSize(object);
        }
        if (object && object.addInfo && 
            object.addInfo.arrayInfo) {
            for (let infoItem of object.addInfo.arrayInfo) {
                if (!infoItem || !infoItem.offset) continue;
                ret += ((infoItem.offset - infoItem.index) | 0) * arrayIndexPointerSize;
            }
        }
        if (object && object.addInfo && 
            object.addInfo.buffInfo) {
            ret += object.addInfo.length | 0;
        }
        return ret;
    }
    getStringContentSize(object) {
        if (!object.stringVal) return 0;
        // ChakraCore uses char16 for each char => 2 bytes
        return 2 * (object.stringVal.length + 1); // string ends with '\0'
    }
    /**
     * reference: http://www.cprogramming.com/tutorial/size_of_class_object.html
     * @param {*} object 
     */
    getObjectContentSize(object) {
        if (!object.properties) return 0;
        return propertyPointerSize * object.properties.length;
    }
}

if (!window.sizeInfo) {
    window.sizeInfo = new SizeInfo();
}

export default window.sizeInfo;


