/**
 * A simple tree component that displays the structure of an object
 * 
 * Author: Liang Gong
 */
import React from 'react';
import ReactDOM from 'react-dom';
import Utils from '../basic/Utils.js';
import TreeView from 'react-treeview';
import sharedObj from '../basic/Global.js';
import NodeVis from './TreeNodeVis.js';
import dataInteface from '../data/DataInterface.js';
import tagGenerator from './TagGenerator.js';

let $ = window.$;

let ComponentUI = (function () {
    /**
     * data structure used by the tree UI
     */
    let dataSource = {loaded: 0};
    let allocTrace = null;
    let props = null;
    /**
     * mapping path to transfomed inner tree UI nodes
     * this caching will rarely be cleared since:
     * 1) there will be limited tree UI nodes rendered during each session
     * 2) the JSON data is not assumed to changed once loaded
     * 3) rendering some tree UI nodes is expensive
     */
    let transformedDataMap = [];
    let computedDataMap = [];
    let transformer = new NodeVis(dataSource); 
    let time = 0;
    /**
     * tree UI component
     */
    const TreeComponent = React.createClass({
        getInitialState() {
            // each TreeComponent has its own subTree Components
            this.subComponents = [];
            let componentId = this.props.componentId ? this.props.componentId : time++;
            return {
                isUnfolded: { allocations: true, snapshot: true },
                visRange: {},
                componentId: componentId,
            };
        },
        componentWillMount() {
            this.loadDataAsync();
        },
        clearAndResetUI() {
            transformedDataMap[this.state.componentId] = undefined;
            computedDataMap[this.state.componentId] = undefined;
            this.resetUI();
        },
        resetUI() {
            // refresh the UI
            if (!transformedDataMap[this.state.componentId]) {
                transformedDataMap[this.state.componentId] = {};
            }
            if (!computedDataMap[this.state.componentId]) {
                computedDataMap[this.state.componentId] = {};
            }
            this.setState({stamp: time++, visRange: []});
        },
        loadDataAsync() {
            // initialize the snapshot data structure
            if (!dataSource.loaded) {
                sharedObj.once('load-snapshot', (snapshotData) => {
                    let snapshot = {snapshot: snapshotData, loaded: true};
                    dataSource = snapshot;
                    dataInteface.setSnapshotJSON(snapshotData);
                    this.clearAndResetUI();
                });
            }
            
            // initialize the props
            if (!props) {
                sharedObj.once('load-prop', (propData) => {
                    props = propData;
                    dataInteface.setPropJSON(props);
                    this.clearAndResetUI();
                });
            } else {
                dataInteface.setPropJSON(props);
            }

            // initialize the alloc trace
            if (!allocTrace) {
                sharedObj.register('load-allocTrace', (trace) => {
                    if (!trace) return;
                    allocTrace = {allocations: trace.allocations, loaded: true};
                    dataInteface.setAllocTraceJSON(trace);
                    this.clearAndResetUI();
                });
            } else {
                dataInteface.setAllocTraceJSON(allocTrace);
            }
            // UI must be reset here;
            this.resetUI();
        },
        handleClick(i) {
            let flags = this.state.isUnfolded;
            flags[i] = !flags[i];
            this.setState({ isUnfolded: flags });
        },
        collapseAll() {
            this.setState({
                isUnfolded: {}
            });
        },
        render() {
            // based on the component's prop (parameter)
            // the tree view will visualize different data
            let obj, pathKey = this.props.path;
            let p, path = pathKey.split('|');
            if (this.props.mainEntry === 'allocTrace') {
                obj = allocTrace ? allocTrace : {allocations: 0, loaded: false};
            } else if (this.props.mainEntry === 'rootList') {
                obj = dataSource;
            }

            if (computedDataMap[this.state.componentId][pathKey]) {
                obj = transformedDataMap[this.state.componentId][pathKey];
                p = path[path.length - 1];
            } else {
                let curPathKey = ''
                for (p of path) {
                    if (curPathKey === '') curPathKey = p;
                    else curPathKey += '|' + p;
                    if (computedDataMap[this.state.componentId][curPathKey]) {
                        // if transformed UI object is computed, use it
                        obj = transformedDataMap[this.state.componentId][curPathKey];
                    } else {
                        // otherwise compute it on-the-fly
                        obj = transformer.transform(obj[p]);
                        if (!obj) { 
                            Utils.log(`[!]: transform return ${obj} when rendering ${p} 
                                       in path: ${pathKey}`);
                            break;
                        }
                        computedDataMap[this.state.componentId][curPathKey] = true;
                        transformedDataMap[this.state.componentId][curPathKey] = obj;
                    }
                }
            }
            
            return (
                <div>
                    {/* <button onClick={this.collapseAll}>Collapse all</button> */}
                    {this.renderTree(obj, p, path)}
                </div>
            );
        },
        renderTree(obj, label, path) {
            if (typeof obj === 'object' && !Array.isArray(obj)) {
                // case: handle object
                return this.renderTreeForObject(obj, label, path);
            } else if (typeof obj === 'object' && Array.isArray(obj)) {
                // handle array
                return this.renderTreeForArray(obj, label, path);
            } else if (typeof obj !== 'object' && typeof obj !== 'function') {
                // handle primitive values
                return this.renderTreeForPrimitive(obj, label, path);
            }
        },
        renderTreeForObject(obj, label, path) {
            let prop, id = path.join('->');
            let fold = !this.state.isUnfolded[id];
            let _thisTreeNode = this;
            // visualize the well known tokens
            let wellKnownTokenTag = tagGenerator.getWellKnownTokenTag(obj);
            // visualize the primitive object's value
            let primitiveObjectValueTag = tagGenerator.getPrimitiveObjectValueTag(obj);
            // visualize the type in the engine
            let displayType = tagGenerator.getDisplayTypeTag(obj);
            // visualize the JavaScript type
            let jsTypeTag = tagGenerator.getJsTypeTag(obj);
            let clickCallback = this.handleClick.bind(null, id);
            // handle the mouse over event
            let mouseOverCallback = () => {
                // display the node attribute
                if (!obj.__META__) return;
                sharedObj.emit('show-data', {
                    data: Utils.stringify(obj.__META__),
                });
                $('#file-name-panel').text('');
            };
            // if this is an allocation site item, or function object
            if (obj && obj.__META__ && 
                (obj.__META__.source || 
                 obj.__META__.__FUNC_BODY__ ||
                 obj.__META__.__SLOT_ITEM__)) {
                mouseOverCallback = () => {
                    // show the file name
                    if (obj.__META__.filename) {
                        $('#file-name-panel').text(
                            obj.__META__.filename
                        );
                    }
                    let source = dataInteface
                        .querySourceCodeByFileName(
                            obj.__META__.filename);
                    // display the source code
                    sharedObj.emit('show-code', {
                        code: source + '',
                        line: obj.__META__.line,
                        column: obj.__META__.column,
                    });
                };
                // attach the special click event handler
                clickCallback = (function() {
                    // fold or unfold
                    _thisTreeNode.handleClick(id);
                    mouseOverCallback();
                });
            }
            // visualize the prefix if exists
            let prefixTag = tagGenerator.getPrefixTag(obj);
            // visualize the size information of a tree node
            let sizeTag = tagGenerator.getSizeTag(obj);
            // visualize the alert icon
            let alertTag = tagGenerator.getAlertTag(obj);
            let infoTag = tagGenerator.getInfoTag(obj);
            // visualize the tag that displays the main entry of this object
            let disTag = tagGenerator.getDisplayTagForObjectNodeInTree(obj, label, {
                _thisTreeNode, wellKnownTokenTag, primitiveObjectValueTag,
                displayType, jsTypeTag, clickCallback, mouseOverCallback,
                prefixTag, sizeTag, alertTag, infoTag
            });
            // generate the subcomponents of the object
            this.subComponents = tagGenerator.getTreeNodeSubComponents(
                {id, treeNode: obj, label, path, fold, parent: this,
                    state: this.state, props: this.props, TreeComponent});
            // generate the final tree view tag
            return tagGenerator.getTreeViewTag(id, disTag, 
                !this.state.isUnfolded[id], this.subComponents, clickCallback, null);
        },
        renderTreeForArray(obj, label, path) {
            let id = path.join('->');
            let fold = !this.state.isUnfolded[id];
             // handle the mouse over event
            let mouseOverCallback = () => {
                // display the node attribute
                if (!obj.__META__) return;
                sharedObj.emit('show-data', {
                    data: Utils.stringify(obj.__META__),
                });
            };
            let clickHandler = this.handleClick.bind(null, id);
            // visualize the size information of a tree node
            let sizeTag = tagGenerator.getSizeTag(obj);
            let args = {id, treeNode: obj, label, path, fold, clickHandler, parent: this,
                    state: this.state, props: this.props, TreeComponent, sizeTag};
            // generate the tag that represents the main entry of the node
            let disTag = tagGenerator.getDisplayTagForArrayNodeInTree(args);            
            // generate the subcomponents of the allocation site
            this.subComponents = tagGenerator.getTreeNodeSubComponents(args);
            // generat the final tree view tag for this component
            return tagGenerator.getTreeViewTag(id, disTag, 
                !this.state.isUnfolded[id], this.subComponents, clickHandler, mouseOverCallback);
        },
        renderTreeForPrimitive(obj, label, path) {
            // skip some meta information
            if (label === '__QUAL_NAME__') return '';
            // start rendering primitive value
            let id = path.join('->');
            let args = {treeNode: obj};
            let clickHandler = this.handleClick.bind(null, id);
            let displayValue = tagGenerator.getPrimitiveTreeNodeValueTag(args);
            // let displayType = (<span>[<span className="type">{typeof obj}</span>]</span>);
            let displayType = '';
            // visualize the JavaScript type
            let jsTypeTag = tagGenerator.getPrimitiveJsTypeTag(obj);
            // visualize the size information of a tree node
            let sizeTag = tagGenerator.getSizeTag(obj);
            args = {
                treeNode: obj, label, path, clickHandler,
                displayValue, displayType, jsTypeTag, sizeTag
            };
            let disTag = tagGenerator.getDisplayTagForPrimitiveNodeInTree(args)
            return tagGenerator.getTreeViewTag(id, disTag, true, '', null, null);
        },
    });
    return TreeComponent;
})();

export default ComponentUI;



