import React from 'react';
import ReactTooltip from 'react-tooltip';
import alertIcon from 'react-icons/lib/io/android-alert.js';

class AlertIconTag extends React.Component {
    componentDidMount() {
        ReactTooltip.rebuild();
    }
    render() {
        return (
            <span data-tip={this.props.tooltipMsg}>
                {React.createElement(alertIcon, {
                    color: 'yellow',
                    size: 14,
                })}
            </span>
        );
    }
}

class AlertIcon {
    constructor() {}
    getTag(treeNode) {
        if (!treeNode || !treeNode.__META__ || !treeNode.__META__.__ISSUES__) {
            return '';
        }
        return (
            <AlertIconTag tooltipMsg={treeNode.__META__.__ISSUES__[0].name} />
        );
    }
}

export default new AlertIcon();



