/**
 * A editor component to show source code
 */
import sharedObj from '../basic/Global.js';
import RtConfig from '../basic/Config.js';

// dummy code to make the compilation process pass
let CodeMirror = window.CodeMirror;
let $ = window.$;

var codeCm = window.CodeMirror(document.getElementById('code-panel'), {
    value: '/* Allocation code will be displayed here.*/',
    readOnly: true,
    lineNumbers: true,
    mode: 'javascript',
    theme: 'dracula',
    styleSelectedText: true,
    scrollbarStyle: 'overlay',
    lineWrapping: true,
});
codeCm.setSize('100%', '100%');

// highlight the corresponding code when
// an allocation position is clicked (or hovered over)
sharedObj.register('show-code', args => {
    codeCm.setValue(args.code);
    if (args.line === undefined) return;
    highlightLine(args.line);
    scrollToLine(args.line);
});

// show data
sharedObj.register('show-data', args => {
    if (!args || !args.data) return;
    if (args.data.length > RtConfig.editorSizeLimit) {
        args.data = args.data.substring(0, RtConfig.editorSizeLimit) + '\n ... ';
    }
    codeCm.setValue(args.data);
});

// fix the position of the code-panel
let positionCodePanelCallback = () => {
    let graphHeight = RtConfig.graphHeight;
    if (!RtConfig.showMemGraph) graphHeight = 0;
    let left = $('#rootlist-panel').offset().left + $('#rootlist-panel').width();
    let height = $(document).height();
    let width = $(document).width();
    let codePanelHeight = height - graphHeight;
    $('#code-panel').css({ top: 0, left, height: codePanelHeight });
    $('#file-name-panel').css({ top: codePanelHeight - 13, left });
    $('#qual-name-panel').css({ top: codePanelHeight, left });
    $('#graph-panel').css({ top: codePanelHeight, left, height: graphHeight });
    if (typeof height === 'number') codePanelHeight += 'px';
    codeCm.setSize(width - left - 60, codePanelHeight);
};
// reposition the code panel when the document is loaded
$(document).ready(positionCodePanelCallback);
// reopsition the code panel when the window is resized
$(window).resize(positionCodePanelCallback);

/**
 * scroll the code editor to a specified line
 * @param {*} ln 
 */
function scrollToLine(ln) {
    var t = codeCm.charCoords({ line: ln, ch: 0 }, 'local').top;
    var middleHeight = codeCm.getScrollerElement().offsetHeight / 2;
    codeCm.scrollTo(null, t - middleHeight - 5);
}
/**
 * highlight a specified line in the code editor
 * @param {*} ln 
 */
function highlightLine(ln) {
    codeCm.markText(
        { line: ln - 1, ch: 0 },
        { line: ln - 1, ch: 100000 },
        { className: 'highlight-code' }
    );
}



