import React from 'react';
import ReactDOM from 'react-dom';
import Tree from './App';
import './view/CodeEditor.js';
import './view/StatusBar.js';
import './view/ScrollBar.js';
import ReactTooltip from 'react-tooltip';
import './view/MemGraph.js';
import registerServiceWorker from './registerServiceWorker';
import './style/index.css';
import './style/tooltip.css';

// render the tree component for allocation sites
ReactDOM.render(
    (<Tree class="tree-root" mainEntry="allocTrace" path="allocations" />),
    document.getElementById('alloc-panel')
);
// render the tree component for root list objects
ReactDOM.render(
    (<Tree mainEntry="rootList" path="snapshot" />),
    document.getElementById('rootlist-panel')
);
// render the tooltip item
ReactDOM.render(
    (<ReactTooltip place="right" type="warning" effect="solid"/>),
    document.getElementById('tool-tip')
);

registerServiceWorker();



