'use strict';

import React from 'react';
import loader from './data/ResourceLoader.js';
import TreeComponent from './view/TreeView.js';

// load the resources
loader.load();

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        return (
            <div>
                <TreeComponent
                    path={this.props.path}
                    mainEntry={this.props.mainEntry}
                />
            </div>
        );
    }
}

export default App;



