/**
 * Utils functions for the view components
 * 
 * Author: Liang Gong
 */
let ViewUtils = {
    _COMMON_NAME_LENGTH: 60,
    /**
     * ensure the __META__ field exists
     * in the tree node
     * @param {*} treeNode 
     */
    ensureMeta(treeNode) {
        if (!treeNode) return;
        if (!treeNode.__META__)
            treeNode.__META__ = {};
    },
    /**
     * make sure the common name of a tree node is short
     * @param {*} treeNode 
     */
    ensureConciseCommonName(treeNode) {
        if (!treeNode || !treeNode.__META__ || 
            !treeNode.__META__.__COMMON_NAME__) return;
        let name = treeNode.__META__.__COMMON_NAME__;
        if (name.length < this._COMMON_NAME_LENGTH) return;
        treeNode.__META__.__FULL_COMMON_NAME__ = name;
        treeNode.__META__.__COMMON_NAME__ = this.shortenCommonName(name)
    },
    shortenCommonName(name) {
        if (!name || typeof name !== 'string') return name;
        if (name < this._COMMON_NAME_LENGTH) return name;
        let arr = name.split('.'), first = true;
        name = '';
        for (let i = arr.length - 1; i >= 0; i--) {
            if (name.length + arr[i].length + 1 > this._COMMON_NAME_LENGTH) break;
            if (first) {
                name = arr[i];
            } else {
                name = arr[i] + '.' + name;
            }
            first = false;
        }
        return name;
    },
    /**
     * check if a tree node has meta data
     * @param {*} treeNode 
     */
    hasMeta(treeNode) {
        return treeNode && treeNode.__META__;
    },
    /**
     * add an issue message to a treeNode
     * @param {*} treeNode 
     * @param {*} alertMsg 
     */
    addIssue(treeNode, alertMsg) {
        this.ensureMeta(treeNode);
        if (!treeNode.__META__.__ISSUES__)
            treeNode.__META__.__ISSUES__ = [];
        treeNode.__META__.__ISSUES__.push(alertMsg);
    },
    /**
     * add an info message to a treeNode
     * @param {*} treeNode 
     * @param {*} infoMsg 
     */
    addInfo(treeNode, infoMsg) {
        this.ensureMeta(treeNode);
        if (!treeNode.__META__.__INFOS__)
            treeNode.__META__.__INFOS__ = [];
        treeNode.__META__.__INFOS__.push(infoMsg);
    },
    /**
     * check if a treeNode represents an object
     * that has the specified type
     * @param {*} treeNode 
     * @param {*} jsTypeName 
     */
    isJsType(treeNode, jsTypeName) {
        if (
            !treeNode ||
            !treeNode.__META__ ||
            !treeNode.__META__.jsTypeName
        )
            return false;
        return treeNode.__META__.jsTypeName === jsTypeName;
    },
    /**
     * check if a tree node has a buffInfo meta data
     * @param {*} treeNode 
     */
    hasBuffInfo(treeNode) {
        if (!this.hasMeta(treeNode)) return false;
        return treeNode.__META__.addInfo &&
            treeNode.__META__.addInfo.buffInfo;
    },
    /**
     * check if a tree node has a arrayInfo meta data
     * @param {*} treeNode 
     */
    hasArrayInfo(treeNode) {
        if (!this.hasMeta(treeNode)) return false;
        return treeNode.__META__.addInfo &&
            treeNode.__META__.addInfo.arrayInfo;
    },
};

export default ViewUtils;


