import Utils from '../../basic/Utils.js';
import dataInterface from '../../data/DataInterface.js';

/**
 * detect sparse array buffers
 */
class SparseArrayBufferChecker {
    constructor() {
        this.sparseThreshold = 0.33;
    }
    check(object, treeNode) {
        // guard checking
        if (!Utils.HOP(object, 'objectId')) return;
        if (!Utils.isJsType(treeNode, 'ArrayBuffer')) return;
        if (!Utils.hasBuffInfo(treeNode)) return;
        // count the empty slots
        let indices = treeNode.__META__.addInfo.buffInfo;
        let emptySlotCnt = 0, totalSlotCnt = indices.length;
        let actualTotalSlotCnt = dataInterface.getArrayLength(object);
        for (let i = 0; i < indices.length; i++) {
            if (indices[i]) continue;
            emptySlotCnt++;
        }
        let estimateFlag = (totalSlotCnt === actualTotalSlotCnt) ? '' : '~';
        // set the flag in the UI tree node
        if (emptySlotCnt / totalSlotCnt >= this.sparseThreshold) {
            Utils.addIssue(treeNode, {
                type: 'sparse-buffer',
                name: `Sparse Buffer (${estimateFlag}${(100 * emptySlotCnt / totalSlotCnt).toFixed(2)}% empty)`,
            });
        }
    }
}

export default new SparseArrayBufferChecker();



