JSTypeInfo.createIndex = function() {
    this.__index = [];
    let propArray = [];
    for (let prop in this) {
        if (!this.hasOwnProperty(prop)) continue;
        if (prop.indexOf('TypeIds_') !== 0) continue;
        propArray.unshift(prop);
    }
    for (let prop of propArray) {
        let id = this[prop][0];
        this.__index[id] = [prop].concat(this[prop].slice(1));
    }
};

JSTypeInfo.getJSTypeName = function(jsTypeId){
    return this.__index[jsTypeId][0].substring(this.prefixLen);
};

JSTypeInfo.getJSTypeOverhead = function(jsTypeId) {
    return this.__index[jsTypeId][1];
};

// class PropertyRecord : public FinalizableObject
//         Field(PropertyId) pid;			// int32
//         Field(bool) isNumeric;
//         Field(bool) isBound;
//         Field(bool) isSymbol;
//         Field(DWORD) byteCount;          // unsign long : 4 bytes
JSTypeInfo.getObjectPropertyPointerSize = function() {
    return 4 + 4 + 1;
};

JSTypeInfo.getArrayIndexPointerSize = function () {
    return RtConfig.pointerSize;
}

JSTypeInfo.createIndex();

export default JSTypeInfo;


