/**
 * A editor component to show memory graph
 */
import sharedObj from '../basic/Global.js';
import RtConfig from '../basic/Config.js';
import dataInterface from '../data/DataInterface.js';
import '../style/graph.css';

(function() {
    if (!RtConfig.showMemGraph) return;

    // dummy code to make the compilation process pass
    let d3 = window.d3;
    let $ = window.$;

    selectableForceDirectedGraph();

    let renderGraph = true;
    // toggle the memory graph rendering
    $(document).keypress(e => {
        var code = e.keyCode || e.which;
        if (code == 13) {
            renderGraph = !renderGraph;
        }
    });

    function selectableForceDirectedGraph() {
        var width = $(document).width() - $('#graph-panel').position().left,
            height = RtConfig.graphHeight,
            shiftKey,
            ctrlKey;

        var nodeGraph = null;
        var xScale = d3.scale.linear().domain([0, width]).range([0, width]);
        var yScale = d3.scale.linear().domain([0, height]).range([0, height]);

        var svg = d3
            .select('#graph-panel')
            .attr('tabindex', 1)
            .on('keydown.brush', keydown)
            .on('keyup.brush', keyup)
            .each(function() {
                this.focus();
            })
            .append('svg')
            .attr('width', width)
            .attr('height', height);

        var zoomer = d3.behavior
            .zoom()
            .scaleExtent([0.1, 10])
            .x(xScale)
            .y(yScale)
            .on('zoomstart', zoomstart)
            .on('zoom', redraw);

        function zoomstart() {
            node.each(function(d) {
                d.selected = false;
                d.previouslySelected = false;
            });
            node.classed('selected', false);
        }

        function redraw() {
            vis.attr(
                'transform',
                'translate(' +
                    d3.event.translate +
                    ')' +
                    ' scale(' +
                    d3.event.scale +
                    ')'
            );
        }

        var brusher = d3.svg
            .brush()
            //.x(d3.scale.identity().domain([0, width]))
            //.y(d3.scale.identity().domain([0, height]))
            .x(xScale)
            .y(yScale)
            .on('brushstart', function(d) {
                node.each(function(d) {
                    d.previouslySelected = shiftKey && d.selected;
                });
            })
            .on('brush', function() {
                var extent = d3.event.target.extent();

                node.classed('selected', function(d) {
                    return (d.selected =
                        d.previouslySelected ^
                        (extent[0][0] <= d.x &&
                            d.x < extent[1][0] &&
                            extent[0][1] <= d.y &&
                            d.y < extent[1][1]));
                });
            })
            .on('brushend', function() {
                d3.event.target.clear();
                d3.select(this).call(d3.event.target);
            });

        var svg_graph = svg.append('svg:g').call(zoomer);
        //.call(brusher)

        var rect = svg_graph
            .append('svg:rect')
            .attr('width', width)
            .attr('height', height)
            .attr('fill', 'transparent')
            // .attr('opacity', 0.5)
            .attr('stroke', 'transparent')
            .attr('stroke-width', 1)
            //.attr("pointer-events", "all")
            .attr('id', 'zrect');

        var brush = svg_graph
            .append('g')
            .datum(function() {
                return { selected: false, previouslySelected: false };
            })
            .attr('class', 'brush');

        var vis = svg_graph.append('svg:g');

        vis
            .attr('fill', 'red')
            .attr('stroke', 'black')
            .attr('stroke-width', 1)
            .attr('opacity', 0.5)
            .attr('id', 'vis');

        brush
            .call(brusher)
            .on('mousedown.brush', null)
            .on('touchstart.brush', null)
            .on('touchmove.brush', null)
            .on('touchend.brush', null);

        brush.select('.background').style('cursor', 'auto');

        var link = vis.append('g').attr('class', 'link').selectAll('line');

        var node = vis.append('g').attr('class', 'node').selectAll('circle');

        let center_view = function() {
            // Center the view on the molecule(s) and scale it so that everything
            // fits in the window

            if (nodeGraph === null) return;

            var nodes = nodeGraph.nodes;

            //no molecules, nothing to do
            if (nodes.length === 0) return;

            // Get the bounding box
            let min_x = d3.min(
                nodes.map(function(d) {
                    return d.x;
                })
            );
            let min_y = d3.min(
                nodes.map(function(d) {
                    return d.y;
                })
            );

            let max_x = d3.max(
                nodes.map(function(d) {
                    return d.x;
                })
            );
            let max_y = d3.max(
                nodes.map(function(d) {
                    return d.y;
                })
            );

            // The width and the height of the graph
            let mol_width = max_x - min_x;
            let mol_height = max_y - min_y;

            // how much larger the drawing area is than the width and the height
            let width_ratio = width / mol_width;
            let height_ratio = height / mol_height;

            // we need to fit it in both directions, so we scale according to
            // the direction in which we need to shrink the most
            let min_ratio = Math.min(width_ratio, height_ratio) * 0.8;

            // the new dimensions of the molecule
            let new_mol_width = mol_width * min_ratio;
            let new_mol_height = mol_height * min_ratio;

            // translate so that it's in the center of the window
            let x_trans = -min_x * min_ratio + (width - new_mol_width) / 2;
            let y_trans = -min_y * min_ratio + (height - new_mol_height) / 2;

            // do the actual moving
            vis.attr(
                'transform',
                'translate(' +
                    [x_trans, y_trans] +
                    ')' +
                    ' scale(' +
                    min_ratio +
                    ')'
            );

            // tell the zoomer what we did so that next we zoom, it uses the
            // transformation we entered here
            zoomer.translate([x_trans, y_trans]);
            zoomer.scale(min_ratio);
        };

        function dragended(d) {
            //d3.select(self).classed("dragging", false);
            node
                .filter(function(d) {
                    return d.selected;
                })
                .each(function(d) {
                    d.fixed &= ~6;
                });
        }

        sharedObj.once('load-snapshot', snapshotData => {
            // start graph initialization
            dataInterface.setSnapshotJSON(snapshotData);
            let graph = {
                nodes: [],
                links: [],
            };
            sharedObj.emit('show-status', 'start building memory graph...');

            let renderSet = [];
            if (!RtConfig.renderFullGraph) {
                // filter the graph, so that only nodes
                // with small in-degree and huge out-degree
                // are rendered
                let inDegrees = [], outDegrees = [];
                dataInterface.traverse((pointerId, pointToId) => {
                    outDegrees[pointerId] |= 0;
                    inDegrees[pointerId] |= 0;
                    outDegrees[pointerId]++;
                    inDegrees[pointToId] |= 0;
                    outDegrees[pointToId] |= 0;
                    inDegrees[pointToId]++;
                });
                // then filter the nodes based on in-degrees and out-degrees
                dataInterface.traverse((pointerId, pointToId) => {
                    if (
                        outDegrees[pointerId] > 20 && inDegrees[pointerId] < 3
                    ) {
                        if(Math.random() < 0.005)
                            renderSet[pointerId] = true;
                    }
                    if (
                        outDegrees[pointToId] > 20 && inDegrees[pointToId] < 3
                    ) {
                        if(Math.random() < 0.005)
                            renderSet[pointToId] = true;
                    }
                });

                // dataInterface.traverse is a blocking function
                // so it should be safe to release inDegrees and outDegrees here
                inDegrees = undefined;
                outDegrees = undefined;
            }
            let objectIdToIdxMap = [];
            function getIdx(objectId) {
                if (objectIdToIdxMap[objectId] === undefined) {
                    graph.nodes.push({
                        objectId: objectId,
                        x: 2000 + (Math.random() * 400) | 0,
                        y: 2000 + (Math.random() * 400) | 0,
                    });
                    objectIdToIdxMap[objectId] = graph.nodes.length - 1;
                }
                return objectIdToIdxMap[objectId];
            }

            dataInterface.traverse((pointerId, pointToId) => {
                if (
                    !RtConfig.renderFullGraph &&
                    !renderSet[pointerId] &&
                    !renderSet[pointToId]
                ) {
                    return;
                }
                let link = {};
                let sourceIdx = getIdx(pointerId);
                let sinkIdx = getIdx(pointToId);
                link.source = sourceIdx;
                link.target = sinkIdx;
                graph.links.push(link);
            });
            // end graph initialization
            // release the render set
            renderSet = undefined;

            // code for drawing arrows
            let defs = svg.append('defs');
            defs
                .append('marker')
                .attr({
                    id: 'arrow',
                    viewBox: '0 -5 10 10',
                    refX: 15,
                    refY: 0,
                    markerWidth: 4,
                    markerHeight: 4,
                    orient: 'auto',
                })
                .append('path')
                .attr('d', 'M0,-5L10,0L0,5')
                .attr('class', 'arrowHead');

            if (RtConfig.renderEdges) {
                link = link
                    .data(graph.links)
                    .enter()
                    .append('line')
                    .attr({
                        class: 'arrow',
                        'marker-end': 'url(#arrow)',
                    })
                    .attr('x1', function(d) {
                        return 0;
                    })
                    .attr('y1', function(d) {
                        return 0;
                    })
                    .attr('x2', function(d) {
                        return 0;
                    })
                    .attr('y2', function(d) {
                        return 0;
                    });
            }
            

            var force = d3.layout
                .force()
                .charge(-20)
                .linkDistance(20)
                .nodes(graph.nodes)
                .links(graph.links)
                .size([width, height])
                .start();

            function dragstarted(d) {
                d3.event.sourceEvent.stopPropagation();
                if (!d.selected && !shiftKey) {
                    // if this node isn't selected, then we have to unselect every other node
                    node.classed('selected', function(p) {
                        return (p.selected = p.previouslySelected = false);
                    });
                }

                d3.select(this).classed('selected', function(p) {
                    d.previouslySelected = d.selected;
                    return (d.selected = true);
                });

                node
                    .filter(function(d) {
                        return d.selected;
                    })
                    .each(function(d) {
                        d.fixed |= 2;
                    });
            }

            function dragged(d) {
                node
                    .filter(function(d) {
                        return d.selected;
                    })
                    .each(function(d) {
                        d.x += d3.event.dx;
                        d.y += d3.event.dy;

                        d.px += d3.event.dx;
                        d.py += d3.event.dy;
                    });

                force.resume();
            }
            node = node
                .data(graph.nodes)
                .enter()
                .append('circle')
                .attr('r', 2)
                .attr('cx', function(d) {
                    return d.x;
                })
                .attr('cy', function(d) {
                    return d.y;
                })
                .on('dblclick', function(d) {
                    d3.event.stopPropagation();
                })
                .on('mouseover', function(d) {
                    $('#qual-name-panel').text(
                        dataInterface.getReadableName(d.objectId)
                    );
                })
                .on('click', function(d) {
                    if (d3.event.defaultPrevented) return;

                    if (!shiftKey) {
                        //if the shift key isn't down, unselect everything
                        node.classed('selected', function(p) {
                            return (p.selected = p.previouslySelected = false);
                        });
                    }

                    // always select this node
                    d3
                        .select(this)
                        .classed(
                            'selected',
                            (d.selected = !d.previouslySelected)
                        );
                })
                .on('mouseup', function(d) {
                    //if (d.selected && shiftKey) d3.select(this).classed("selected", d.selected = false);
                })
                .call(
                    d3.behavior
                        .drag()
                        .on('dragstart', dragstarted)
                        .on('drag', dragged)
                        .on('dragend', dragended)
                );

            function tick() {
                if (RtConfig.renderEdges) {
                    link.attr({
                        class: 'arrow',
                        'marker-end': 'url(#arrow)',
                    })
                    .attr('x1', function(d) {
                        return d.source.x;
                    })
                    .attr('y1', function(d) {
                        return d.source.y;
                    })
                    .attr('x2', function(d) {
                        return d.target.x;
                    })
                    .attr('y2', function(d) {
                        return d.target.y;
                    });
                }
                

                node
                    .attr('cx', function(d) {
                        return d.x;
                    })
                    .attr('cy', function(d) {
                        return d.y;
                    });
            }

            var moveItems = (function() {
                var todoNode = 0;
                var todoLink = 0;
                var MAX_NODES = RtConfig.maxNodesRendered;
                var MAX_LINKS = RtConfig.maxLinksRendered;
                var restart = false;

                function moveSomeNodes() {
                    var n;
                    var goal = Math.min(todoNode + MAX_NODES, node[0].length);

                    for (var i = todoNode; i < goal; i++) {
                        n = node[0][i];
                        n.setAttribute('cx', n.__data__.x);
                        n.setAttribute('cy', n.__data__.y);
                    }

                    todoNode = goal;
                    setTimeout(requestAnimationFrame.bind(null, moveSome), RtConfig.memGraphRenderInterval);
                }

                function moveSomeLinks() {
                    var l;
                    var goal = Math.min(todoLink + MAX_LINKS, link[0].length);

                    for (var i = todoLink; i < goal; i++) {
                        l = link[0][i];
                        //console.log(l);
                        l.setAttribute('x1', l.__data__.source.x);
                        l.setAttribute('y1', l.__data__.source.y);
                        l.setAttribute('x2', l.__data__.target.x);
                        l.setAttribute('y2', l.__data__.target.y);
                    }

                    todoLink = goal;
                    setTimeout(requestAnimationFrame.bind(null, moveSome), RtConfig.memGraphRenderInterval);
                }

                function moveSome() {
                    //console.time('moveSome')
                    if (todoNode < node[0].length) moveSomeNodes();
                    else {
                        // nodes are done
                        if (RtConfig.renderEdges && todoLink < link[0].length) moveSomeLinks();
                        else {
                            // both nodes and links are done
                            if (restart) {
                                restart = false;
                                todoNode = 0;
                                todoLink = 0;
                                setTimeout(requestAnimationFrame.bind(null, moveSome), RtConfig.memGraphRenderInterval);
                            }
                        }
                    }
                }

                return function moveItems() {
                    if (!restart) {
                        restart = true;
                        // requestAnimationFrame(moveSome);
                        setTimeout(requestAnimationFrame.bind(null, moveSome), RtConfig.memGraphRenderInterval);
                    }
                };
            })();
            var count = 0;
            force.on('tick', () => {
                if (count++ % 30 === 1 && renderGraph) {
                    moveItems();
                }
            });

            // force.on('tick', tick);
        });

        function keydown() {
            shiftKey = d3.event.shiftKey || d3.event.metaKey;
            ctrlKey = d3.event.ctrlKey;

            console.log('d3.event', d3.event);

            if (d3.event.keyCode == 67) {
                //the 'c' key
                center_view();
            }

            if (shiftKey) {
                svg_graph
                    .call(zoomer)
                    .on('mousedown.zoom', null)
                    .on('touchstart.zoom', null)
                    .on('touchmove.zoom', null)
                    .on('touchend.zoom', null);

                //svg_graph.on('zoom', null);
                vis.selectAll('g.gnode').on('mousedown.drag', null);

                brush.select('.background').style('cursor', 'crosshair');
                brush.call(brusher);
            }
        }

        function keyup() {
            shiftKey = d3.event.shiftKey || d3.event.metaKey;
            ctrlKey = d3.event.ctrlKey;

            brush
                .call(brusher)
                .on('mousedown.brush', null)
                .on('touchstart.brush', null)
                .on('touchmove.brush', null)
                .on('touchend.brush', null);

            brush.select('.background').style('cursor', 'auto');
            svg_graph.call(zoomer);
        }
    }
})();



